import { ILoading } from '@microsoft/portal-app/lib/models/ILoading';
import { newGuid } from '../../../utilities/guid';
import { IPermission } from './IPermission';
import { ISelectedRoleDefinition } from './ISelectedRoleDefinition';

export interface IRoleDefinition {
  id: string;
  name: string;
  description: string;
  permissions: IPermission[];
  scopes: string[];
  createdBy?: string;
  createdOn?: string; // TODO: change to date later
  updatedBy?: string;
  updatedOn?: string; // TODO: change to date later
  isBuiltIn: boolean;
}

export interface IListRDsState extends ILoading {
  currentAppId: string;
  roleDefinitions: IRoleDefinition[];
  hashIndexRDs: number[];
}

export interface ITableRoleDefinition extends ILoading {
  listRoleDefinition: IRoleDefinition[];
}

export const EMPTY_ROLE_DEFINITION_WITH_LOADING: ISelectedRoleDefinition = {
  id: newGuid(),
  name: '',
  description: '',
  scopes: [''],
  permissions: [
    {
      actions: [''],
      notActions: [],
      condition: null
    }
  ],
  isBuiltIn: true,
  createdBy: '',
  createdOn: '',
  updatedBy: '',
  updatedOn: '',
  isLoading: false
};



