import { IWithResponsiveModeState } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import { InjectedTranslateProps } from 'react-i18next';
import { IApplication } from '../../../models/aadPAP/application/IApplication';
import { IRoleAssignmentQueryParams } from '../../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { IRoleDefinition } from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { ISelectedRoleDefinition } from '../../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { AuthzPanelType } from '../RoleDefinition.types';

/**
 * Used as a standalone interface by mapStateToProps, the IRoleDefinitionPanelMappedProps defines the non action properties.
 * for the RoleDefinitionPanel control.
 */
export interface IRoleDefinitionPanelMappedProps {
  selectedRoleDefinition: ISelectedRoleDefinition;
  roleAssignmentQueryParams: IRoleAssignmentQueryParams;
  currentNpd: IApplication;
}

/**
 * Used as a standalone interface by mapDispatchToProps, the IRoleDefinitionPanelActions interface defines the actions
 * for the RoleDefinitionPanel control
 */
export interface IRoleDefinitionPanelActions {
  getListRAs: (queryRA: IRoleAssignmentQueryParams) => void;
  updateRoleDefinition(roleDef: IRoleDefinition): void;
  deleteRoleDefinition(roleDef: IRoleDefinition): void;
}

/**
 * The props for the RoleDefinitionPanel control.
 */
export interface IRoleDefinitionPanelProps
  extends IRoleDefinitionPanelActions,
    IRoleDefinitionPanelMappedProps,
    IConnectedRoleDefinitionPanelProps,
    InjectedTranslateProps,
    IWithResponsiveModeState {}

/**
 * The props for the redux connected RoleDefinitionPanel
 * @description Called "own" props in the redux function mapStateToProps.
 * The mapStateToProps can map the properties of this interface to the IRoleDefinitionPanelProps interface.
 */
export interface IConnectedRoleDefinitionPanelProps {
  showPanel: boolean;
  onDismiss: () => void;
  // isNewItem: boolean;
  roleDefinitionPanelType: FormPanelType;
  showRAPanel: (
    panelType: AuthzPanelType,
    formPanelType: FormPanelType,
    showPanel: boolean
  ) => void;
}

/**
 * Represents the RoleDefinitionPanel control's internal state
 */
export interface IRoleDefinitionPanelState {
  isEditDisabled: boolean;
}

export enum RoleDefinitionPivots {
  BasicProperties = 'Basic Properties',
  Permissions = 'Permissions',
  AssignedUsers = 'Assigned User'
}

export enum FormPanelType {
  new = 'new',
  edit = 'edit',
  view = 'view',
  delete = 'delete',
  assign = 'assign'
}



