import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../../models';
import { IRoleAssignmentQueryParams } from '../../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { DetailObjectInfo } from './DetailObjectInfo';
import {
  IConnectedDetailObjectInfoProps,
  IDetailObjectInfoActions,
  IDetailObjectInfoMappedProps
} from './DetailObjectInfo.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the ListPage control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedDetailObjectInfoProps
): IDetailObjectInfoMappedProps => {
  const { currentNpd, roleAssignmentQueryParams } = state.app;
  return {
    currentNpd,
    roleAssignmentQueryParams
  };
};

/**
 * Maps the dispatch actions to the props for the ListPage control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapDispatchToProps = (dispatch: Dispatch<IRootAuthzState>): IDetailObjectInfoActions => {
  const getRoleAssignmentsAction = createAction<IRoleAssignmentQueryParams>(
    AuthzActions.getRoleAssignmentList,
    dispatch
  );
  const updateQueryAction = createAction<IRoleAssignmentQueryParams>(
    AuthzActions.updateRoleAssignmentListQuery,
    dispatch
  );
  return {
    getListRAs: (queryRA: IRoleAssignmentQueryParams) => getRoleAssignmentsAction(queryRA),
    updateRAQuery: (queryRA: IRoleAssignmentQueryParams) => updateQueryAction(queryRA)
  };
};

/**
 * A redux connected ListPage control.
 * @description Supports translation
 */
export const ConnectedDetailObjectInfo = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('OneAuthZ')(DetailObjectInfo));



