import { Formik, FormikActions, FormikProps } from 'formik';
import {
  classNamesFunction,
  CommandBar,
  css,
  DefaultButton,
  IContextualMenuItem,
  IPanelProps,
  IPanelStyleProps,
  IPanelStyles,
  Panel,
  PanelType,
  PrimaryButton
} from 'office-ui-fabric-react';
import { withResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import {
  getEmptySelectedRoleAssignment,
  ISelectedRoleAssignment
} from '../../../models/aadPAP/roleAssignment/ISelectedRoleAssignment';
import { IRoleDefinition } from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { CUSTOMIZED_PANEL_STYLE } from '../../../Styles/PANEL_STYLES_RD_RA';
import { FormPanelType } from '../../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import { ConnectedRoleAssignmentForm } from '../RoleAssignmentForm/ConnectedRoleAssignmentForm';
import { RoleAssignmentFormValue } from '../RoleAssignmentForm/RoleAssignmentForm.types';
import { getClassNames } from './RoleAssignmentPanel.classNames';
import {
  IRoleAssignmentPanelProps,
  IRoleAssignmentPanelState
} from './RoleAssignmentPanel.type';

export const getPanelClassNames = classNamesFunction<
  IPanelStyleProps,
  IPanelStyles
>();

@withResponsiveMode
export class RoleAssignmentPanel extends React.Component<
  IRoleAssignmentPanelProps,
  IRoleAssignmentPanelState
> {
  public static defaultProps = {
    roleAssignmentPanelType: FormPanelType.view,
    showPanel: false
  };
  constructor(props: IRoleAssignmentPanelProps) {
    super(props);

    this.state = {
      isEditDisabled: props.roleAssignmentPanelType !== FormPanelType.new,
      isInCreateNewItemMode: true,
      roleAssignment: {} as ISelectedRoleAssignment
    };
  }
  public componentWillReceiveProps(nextProps: IRoleAssignmentPanelProps) {
    // do not change this line Bill Wang
    if (this.props.showPanel === false && nextProps.showPanel === true) {
      const {
        roleAssignmentPanelType,
        selectedRoleAssignment,
        selectedRoleDefinition
      } = nextProps;
      let roleAssignment = selectedRoleAssignment as ISelectedRoleAssignment;
      if (roleAssignmentPanelType === FormPanelType.new) {
        roleAssignment = getEmptySelectedRoleAssignment();
      } else if (roleAssignmentPanelType === FormPanelType.assign) {
        roleAssignment = getEmptySelectedRoleAssignment();
        roleAssignment.roleDefinitionId = selectedRoleDefinition.id;
        roleAssignment.nameRD = selectedRoleDefinition.id;
        roleAssignment.scope = selectedRoleDefinition.scopes[0];
        roleAssignment.roleDefinition = selectedRoleDefinition as IRoleDefinition;
      }
      this.setState({
        roleAssignment: roleAssignment
      });
    }
  }

  public render(): React.ReactNode {
    let panel = (
      <div>
        <Formik
          initialValues={new RoleAssignmentFormValue(this.state.roleAssignment)}
          validationSchema={RoleAssignmentFormValue.validationSchema}
          enableReinitialize={true}
          onSubmit={this._handleSubmit}
          render={this._renderForm}
        />
      </div>
    );
    return panel;
  }

  private _renderForm = (formikProps: FormikProps<RoleAssignmentFormValue>) => {
    const {
      showPanel,
      roleAssignmentPanelType,
      listRoleDefinitions
    } = this.props;
    const { isEditDisabled } = this.state;

    return (
      <Panel
        isOpen={showPanel}
        onDismiss={this._handleDismiss}
        isLightDismiss={true}
        type={PanelType.medium}
        headerText="Role Assignment"
        styles={CUSTOMIZED_PANEL_STYLE}
        closeButtonAriaLabel="Close"
        onRenderHeader={this._renderHeader}
        onRenderFooterContent={() => this._onRenderFooter(formikProps)}
      >
        <ConnectedRoleAssignmentForm
          listRoleDefinitions={listRoleDefinitions}
          roleAssignment={formikProps.values}
          roleAssignmentPanelType={roleAssignmentPanelType}
          // onUpdate={this.updateRoleAssignment}
          // onDelete={this._deleteRoleAssignment}
          formikProps={formikProps}
          isEditDisabled={isEditDisabled}
        />
      </Panel>
    );
  }

  private _handleSubmit = (
    values: RoleAssignmentFormValue,
    actions: FormikActions<RoleAssignmentFormValue>
  ) => {
    this._updateRoleAssignment(values);
    actions.setSubmitting(true);
    this.setState({ isEditDisabled: true });
  }

  private _handleDismiss = () => {
    const { onDismiss, roleAssignmentPanelType } = this.props;
    onDismiss!();
    roleAssignmentPanelType !== FormPanelType.new
      ? this.setState({ isEditDisabled: true })
      : undefined;
  }

  private _onRenderFooter = (
    formikProps: FormikProps<RoleAssignmentFormValue>
  ): JSX.Element => {
    const { isEditDisabled } = this.state;
    const { roleAssignmentPanelType } = this.props;
    return (
      <div>
        <PrimaryButton
          type="submit"
          onClick={formikProps.submitForm}
          disabled={
            roleAssignmentPanelType !== FormPanelType.new &&
            roleAssignmentPanelType !== FormPanelType.assign
              ? formikProps.isSubmitting ||
                !formikProps.dirty ||
                !formikProps.isValid ||
                isEditDisabled
              : formikProps.isSubmitting ||
                !formikProps.dirty ||
                !formikProps.isValid
          }
        >
          Submit
        </PrimaryButton>
        &nbsp;
        <DefaultButton
          type="button"
          onClick={() => formikProps.resetForm()}
          disabled={
            roleAssignmentPanelType !== FormPanelType.new
              ? formikProps.isSubmitting || !formikProps.dirty || isEditDisabled
              : formikProps.isSubmitting || !formikProps.dirty
          }
        >
          Reset
        </DefaultButton>
      </div>
    );
  }

  private _renderHeader = (
    props: IPanelProps | undefined,
    _defaultRender?: (props?: IPanelProps) => JSX.Element | null
  ): JSX.Element | null => {
    const {
      headerText,
      componentId,
      headerClassName = '',
      styles,
      theme
    } = props!;
    const classNames = getClassNames();
    const panelClassNames = getPanelClassNames(styles, { theme: theme! });
    const { roleAssignmentPanelType } = this.props;

    if (headerText) {
      return (
        <React.Fragment>
          <div
            className={css(
              'ms-Panel-header',
              panelClassNames.header,
              classNames.header
            )}
          >
            <p
              className={css(
                'ms-Panel-headerText',
                panelClassNames.headerText,
                classNames.headerText,
                headerClassName
              )}
              id={componentId + '-headerText'}
              role="heading"
              aria-level={1}
            >
              {headerText}
            </p>
          </div>
          {roleAssignmentPanelType !== FormPanelType.new &&
          roleAssignmentPanelType !== FormPanelType.assign ? (
            <div
              className={css(
                'ms-DetailsListHeaderActions'
                // detailPanelStyles.commandBar
              )}
            >
              <CommandBar
                items={this._getCommandBarItems()}
                ariaLabel={'Role Assignment Form commands'}
              />
            </div>
          ) : (
            undefined
          )}
        </React.Fragment>
      );
    }
    return null;
  }

  private _getCommandBarItems = (): IContextualMenuItem[] => {
    const { roleAssignmentPanelType, selectedRoleAssignment } = this.props;

    return roleAssignmentPanelType !== FormPanelType.new
      ? [
          {
            key: 'delete',
            name: 'Delete',
            iconProps: {
              iconName: 'Delete'
            },
            ariaLabel: 'Delete Role Definition',
            onClick: () => {
              this._deleteRoleAssignment(selectedRoleAssignment);
            }
          },
          {
            key: 'edit',
            name: 'Edit',
            iconProps: {
              iconName: 'Edit'
            },
            onClick: () => this.setState({ isEditDisabled: false })
          }
        ]
      : [];
  }

  private _deleteRoleAssignment = (
    roleAssignment: ISelectedRoleAssignment
  ): void => {
    this.props.deleteRoleAssignment(roleAssignment);
    if (!roleAssignment.isLoading) {
      this.props.onDismiss!();
    }
  }

  private _updateRoleAssignment = (
    roleAssignment: ISelectedRoleAssignment
  ): void => {
    this.props.updateRoleAssignment(roleAssignment);
    if (!roleAssignment.isLoading) {
      this.props.onDismiss!();
    }
  }
}



