import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../../models';
import { IGraphObjectInfo } from '../../../models/graph';
import { RoleAssignmentForm } from './RoleAssignmentForm';
import {
  IConnectedRoleAssignmentFormProps,
  IRoleAssignmentFormActions,
  IRoleAssignmentFormMappedProps
} from './RoleAssignmentForm.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the ListPage control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleAssignmentFormProps
): IRoleAssignmentFormMappedProps => {
  const { listGraphObjects, bulkGraphInfos } = state.app;
  return {
    listGraphObjects,
    bulkGraphInfos
  };
};

/**
 * Maps the dispatch actions to the props for the ListPage control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): IRoleAssignmentFormActions => {
  const getGraphObjectsAction = createAction<IGraphObjectInfo[]>(
    AuthzActions.getGraphObjectList,
    dispatch
  );
  return {
    getGraphObjectList: (graphObjectIds: IGraphObjectInfo[]) => {
      getGraphObjectsAction(graphObjectIds);
    },
    getBulkGraphInfos: createAction<{ bulkObjectKeyWords: string }>(
      AuthzActions.getBulkGraphInfos,
      dispatch
    )
  };
};

/**
 * A redux connected ListPage control.
 * @description Supports translation
 */
export const ConnectedRoleAssignmentForm = connect(
  mapStateToProps,
  mapDispatchToProps
)(RoleAssignmentForm);



