import { css } from '@uifabric/utilities';
import {
  ChoiceGroup,
  IChoiceGroupOption,
  Persona,
  TextField
} from 'office-ui-fabric-react';
import {
  CheckboxVisibility,
  DetailsList,
  DetailsListLayoutMode,
  IColumn,
  IDetailsRowProps,
  Selection
} from 'office-ui-fabric-react/lib/DetailsList';
import * as React from 'react';
import { isNullOrUndefined } from 'util';
import {
  emptySearchGraphObjectsInfo,
  GraphObjectTypes,
  IGraphObjectInfo,
  ISearchGraphObjectsInfo
} from '../../../models/graph';
import { getColAADSysRoleAssigns } from '../../../models/graph/ITblSearchGraphObjectsInfo';
import { renderEveryOtherRowSearchObject } from '../../../utilities/fabric';
import { getResponsiveMode } from '../../../utilities/responsiveMode';
import Loading from '../../Common/Loading/Loading';
import {
  ContentSearchObjsType,
  IContentSearchObjsProps,
  IContentSearchObjsState,
  optionStyles
} from './ContentSearchObjs.types';

const pageLayoutStyles = require('../../../Styles/PageLayout.scoped.scss');
const searchObjsPgStyles = require('./ContentSearchObjs.scoped.scss');

export class ContentSearchObjs extends React.Component<
  IContentSearchObjsProps,
  IContentSearchObjsState
> {
  private _selection: Selection;
  constructor(props: IContentSearchObjsProps) {
    super(props);
    this._selection = new Selection();

    const initTblSearchGraphObjectsInfo: ISearchGraphObjectsInfo = {
      ...emptySearchGraphObjectsInfo
    };

    // TODO: Change to constants variable later
    let columns: IColumn[] =
      this.props.contentSearchObjsType === ContentSearchObjsType.panel
        ? [
            {
              key: 'principal',
              name: props.t('Principal'),
              className: 'ms-pii',
              fieldName: 'principal',
              minWidth: 1,
              onRender: this._renderCell
            }
          ]
        : getColAADSysRoleAssigns(
            getResponsiveMode(this.props.responsiveMode),
            this.props.t!,
            GraphObjectTypes.User
          );

    this.state = {
      columns: columns,
      tblSearchGraphObjectsInfo: initTblSearchGraphObjectsInfo,
      keyWords: decodeURIComponent(initTblSearchGraphObjectsInfo.keyWords!),
      objType: initTblSearchGraphObjectsInfo.objectType
    };
  }

  public componentWillReceiveProps(nextProps: IContentSearchObjsProps): void {
    if (
      nextProps.searchGraphObjectsInfo !==
        this.state.tblSearchGraphObjectsInfo &&
      !nextProps.searchGraphObjectsInfo.isLoading
    ) {
      this.setState({
        tblSearchGraphObjectsInfo: nextProps.searchGraphObjectsInfo
      });
    }
  }

  public render(): React.ReactNode {
    const { t } = this.props;
    return (
      <div
        className={css(pageLayoutStyles.content, searchObjsPgStyles.content)}
      >
        <div className={css(pageLayoutStyles.msGridRow)}>
          <ChoiceGroup
            defaultSelectedKey="user"
            onChanged={this.onChangeOption}
            selectedKey={this.state.objType}
            options={[
              {
                key: GraphObjectTypes.User,
                iconProps: { iconName: 'Contact' },
                text: t('User'),
                styles: optionStyles
              },
              {
                key: GraphObjectTypes.Group,
                iconProps: { iconName: 'Group' },
                text: t('Group'),
                styles: optionStyles
              },
              {
                key: GraphObjectTypes.ServicePrincipal,
                iconProps: { iconName: 'AppIconDefault' },
                text: t('Application'),
                styles: optionStyles
              }
            ]}
          />
        </div>
        <div className={css(pageLayoutStyles.msGridRow)}>
          <TextField
            id="input_GraphSearch"
            onChanged={this.onChangeKeyValue}
            onKeyPress={this.onKeyPress}
            placeholder={t('Search by Name, Email or Application')}
            ariaLabel={t('Search by Name, Email or Application')}
            value={this.state.keyWords}
          />
        </div>
        {!this.props.searchGraphObjectsInfo.isLoading ? (
          <DetailsList
            className={
              this.props.contentSearchObjsType === ContentSearchObjsType.ssm
                ? css(searchObjsPgStyles.listGraphObjectsSmall)
                : css(searchObjsPgStyles.listGraphObjects)
            }
            items={this.state.tblSearchGraphObjectsInfo.graphObjectsInfo!}
            columns={this.state.columns}
            selection={this._selection}
            selectionPreservedOnEmptyClick={true}
            layoutMode={DetailsListLayoutMode.justified}
            onRenderItemColumn={this._renderItemColumn}
            onRenderRow={(props: IDetailsRowProps | undefined) =>
              renderEveryOtherRowSearchObject(
                props,
                this.props.selectObject,
                this._selection
              )
            }
            checkboxVisibility={CheckboxVisibility.hidden}
            // disableSelectionZone={true}
            ariaLabelForListHeader=""
            ariaLabelForGrid={this.props.t('Search Objects')}
          />
        ) : (
          <Loading
            isSmallWindow={
              this.props.contentSearchObjsType === ContentSearchObjsType.ssm ||
              this.props.contentSearchObjsType === ContentSearchObjsType.panel
            }
          />
        )}
      </div>
    );
  }

  private _renderItemColumn = (
    item: IGraphObjectInfo,
    _index: number | undefined,
    column: IColumn | undefined
  ): React.ReactNode => {
    if (column === undefined) {
      return <React.Fragment />;
    }
    return (
      <span
      // onClick={() => {
      //   this.props.selectObject(item);
      // }}
      >
        {item[column.fieldName as keyof IGraphObjectInfo] as string}
      </span>
    );
  }

  private onKeyPress = (e: React.KeyboardEvent) => {
    if (e.key !== 'Enter') {
      return;
    }
    this.searchObjects();
  }

  private onChangeKeyValue = (e: React.FormEvent<HTMLInputElement>) => {
    this.setState({ keyWords: (e as unknown) as string }, () => {
      // if (this.state.keyWords === "")
      // this.props.resetSearchObjects();  e.currentTarget.
    });
  }
  private _renderCell = (item: IGraphObjectInfo): React.ReactNode => {
    switch (item.objectType) {
      case GraphObjectTypes.ServicePrincipal:
        return (
          <div
            className="ms-CardListItem"
            data-is-focusable={true}
            onClick={() => {
              this.props.selectObject(item);
            }}
          >
            <Persona
              text={item.displayName}
              secondaryText={item.objectId}
              tertiaryText={item.homepage}
            />
          </div>
        );
      case GraphObjectTypes.Group:
        return (
          <div
            className="ms-CardListItem"
            data-is-focusable={true}
            onClick={() => {
              this.props.selectObject(item);
            }}
          >
            <Persona
              text={item.displayName}
              secondaryText={item.mail}
              tertiaryText={item.description}
            />
          </div>
        );
      case GraphObjectTypes.User:
        return (
          <div
            className="ms-CardListItem"
            data-is-focusable={true}
            onClick={() => {
              this.props.selectObject(item);
            }}
          >
            <Persona
              text={item.displayName}
              secondaryText={item.userPrincipalName}
              tertiaryText={item.jobTitle}
            />
          </div>
        );

      default:
        return (
          <div
            className="ms-CardListItem"
            data-is-focusable={true}
            onClick={() => {
              this.props.selectObject(item);
            }}
          >
            <Persona
              text={item.displayName}
              secondaryText={item.userPrincipalName}
              tertiaryText={item.jobTitle}
            />
          </div>
        );
    }
  }

  private onChangeOption = (option: IChoiceGroupOption) => {
    const { t } = this.props;
    const objType = option.key as GraphObjectTypes;
    let columns = [];
    if (this.props.contentSearchObjsType === ContentSearchObjsType.panel) {
      columns.push({
        key: 'principal',
        name: t('Principal'),
        className: 'ms-pii',
        fieldName: 'principal',
        minWidth: 1,
        onRender: this._renderCell
      });
    } else {
      columns = getColAADSysRoleAssigns(
        getResponsiveMode(this.props.responsiveMode),
        this.props.t!,
        objType
      );
    }

    this.setState(
      {
        objType: objType,
        columns: columns
      },
      () => {
        this.searchObjects();
        // if (this.state.keyWords === "")
        //   this.props.resetSearchObjects();
      }
    );
  }

  private searchObjects() {
    if (!isNullOrUndefined(this.state.keyWords) && this.state.keyWords !== '') {
      this.props.searchGraphInfo({
        objectType: this.state.objType,
        keyWords: encodeURIComponent(this.state.keyWords)
      });
    }
  }
}



