import { css } from '@uifabric/utilities';
import { Field, FormikProps } from 'formik';
import {
  BaseButton,
  Button,
  IconButton,
  IDropdownOption,
  Pivot,
  PivotItem,
  PivotLinkFormat
} from 'office-ui-fabric-react';
import * as React from 'react';
import { translate } from 'react-i18next';
import { ReactNode } from 'react-redux';
import { isNullOrUndefined } from 'util';
import { ISelectedRoleAssignment } from '../../../models/aadPAP/roleAssignment/ISelectedRoleAssignment';
import { EMPTY_ROLE_DEFINITION_WITH_LOADING } from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { ISelectedRoleDefinition } from '../../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { GraphObjectTypes } from '../../../models/graph/GraphObjectTypes';
import { IBufferGraphObjects } from '../../../models/graph/IBufferGraphObjects';
import { IGraphObjectInfo } from '../../../models/graph/IGraphObjectInfo';
import { getObjInfoFromBuffer } from '../../../redux/actions/graph/getObjInfoFromBuffer';
import { STYLES_SEARCH_GRAPH_ICON } from '../../../Styles/STYLES_FILTER_QUERY_LIST_RAS';
import { isGuid } from '../../../utilities/guid';
import Loading from '../../Common/Loading/Loading';
import { FormikDropdown } from '../../Forms/FormItem/FormikDropdown';
import { FormTextField } from '../../Forms/FormItem/FormTextField';
import { FormPanelType } from '../../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import {
  ConnectedContentSearchObjs,
  ContentSearchObjsType
} from '../../SearchObjects/ContentSearchObjects';
import { IListGraphObjsState } from '../RoleAssignmentsTable/RoleAssignmentsTable.types';
import {
  IRoleAssignmentFormProps,
  IRoleAssignmentFormState,
  RoleAssignmentFormValue
} from './RoleAssignmentForm.types';

const detailPanelStyles = require('../../../Styles/PanelList.scoped.scss');
const raPanelStyles = require('./RoleAssignmentForm.scoped.scss');

class RoleAssignmentFormComponent extends React.Component<
  IRoleAssignmentFormProps,
  IRoleAssignmentFormState
> {
  constructor(props: IRoleAssignmentFormProps) {
    super(props);
    this.state = {
      // isEditDisabled: true,
      // roleDefintionId: this.props.roleAssignment.roleDefinitionId,
      showModal: false
    };
    this.props.formikProps.resetForm(
      this._addCreateUpdateByPrincipal(
        this.props.formikProps.values,
        this.props.listGraphObjects.bufferGraphObjects
      )
    );
  }

  public componentWillReceiveProps(nextProps: IRoleAssignmentFormProps) {
    if (
      this.props.bulkGraphInfos.isLoading === true &&
      nextProps.bulkGraphInfos.isLoading === false
    ) {
      console.log(nextProps.bulkGraphInfos.lstGraphObjects);
    }
    if (
      this.props.listGraphObjects.isLoading &&
      !nextProps.listGraphObjects.isLoading
    ) {
      if (
        !isNullOrUndefined(this.props.listGraphObjects.bufferGraphObjects) &&
        this.props.listGraphObjects.bufferGraphObjects.lstGraphObjects.length >
          0 &&
        !isNullOrUndefined(
          this.props.listGraphObjects.bufferGraphObjects.hashIndexObjs
        ) &&
        !isNullOrUndefined(
          nextProps.listGraphObjects.bufferGraphObjects.hashIndexObjs[
            this.props.formikProps.values.principalId
          ]
        )
      ) {
        let graphObject =
          nextProps.listGraphObjects.bufferGraphObjects.lstGraphObjects[
            nextProps.listGraphObjects.bufferGraphObjects.hashIndexObjs[
              nextProps.formikProps.values.principalId
            ]
          ];

        this._selectObject(graphObject);
      }

      this.props.formikProps.resetForm(
        this._updateCreateUpdateByPrincipal(
          this.props.formikProps,
          nextProps.listGraphObjects.bufferGraphObjects
        )
      );
    }
  }

  public render(): React.ReactNode {
    const { handleSubmit } = this.props.formikProps;
    let form = (
      <div
        className={css(
          'ms-PanelFormContent',
          detailPanelStyles.panelFormBorder
        )}
      >
        <form
          onSubmit={handleSubmit}
          className={css(
            'ms-PanelFormContent',
            detailPanelStyles.panelFormContent
          )}
        >
          <Pivot linkFormat={PivotLinkFormat.links}>
            <PivotItem
              headerText="Basic Properties"
              className={css(detailPanelStyles.panelTabContent)}
            >
              {this._renderBasicPropertiesTab()}
            </PivotItem>
            <PivotItem
              headerText="Role Definition Information"
              className={css(detailPanelStyles.panelTabContent)}
            >
              {this._renderRoleDefinitionInfoTab()}
            </PivotItem>
          </Pivot>
        </form>
      </div>
    );
    return (
      <React.Fragment>
        <div
          data-automation-id="roleAssignmentContent"
          className="ms-DetailsContent"
        >
          {!this.props.formikProps.values.isLoading ? (
            <div
              className={css(
                'ms-DetailsContentCard',
                detailPanelStyles.detailsContentCard
              )}
            >
              <div className={css('ms-Grid', detailPanelStyles.cardFields)}>
                <div className="ms-Grid-row">{form}</div>
              </div>
            </div>
          ) : (
            <Loading />
          )}
        </div>
        {/* <JsonView data={{ values, touched, errors }} /> */}
      </React.Fragment>
    );
  }

  private _renderRoleDefinitionInfoTab = (): React.ReactNode => {
    const { values } = this.props.formikProps;
    return (
      <React.Fragment>
        <div className={detailPanelStyles.fieldItem}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Role Name')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {values.roleDefinition.name}
          </div>
        </div>
        <div className={detailPanelStyles.fieldItem}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Scopes')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {values.roleDefinition.scopes !== undefined
              ? values.roleDefinition.scopes.map(
                  (value: string, index: number) => {
                    return <div key={index}>{value}</div>;
                  }
                )
              : null}
          </div>
        </div>
        <div className={detailPanelStyles.fieldItem}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Permissions')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {JSON.stringify(values.roleDefinition!.permissions!)}
          </div>
        </div>
      </React.Fragment>
    );
  }

  private _renderBasicPropertiesTab = (): React.ReactNode => {
    const { isEditDisabled, roleAssignmentPanelType } = this.props;
    const { values } = this.props.formikProps;
    let roleAssignmentIdField = this._getRoleAssignmentIdField();
    let roleDefinitionNameField,
      roleDefinitionIdField,
      principleIdField,
      scopeField,
      conditionField,
      createAndUpdateInfo = undefined;
    switch (roleAssignmentPanelType) {
      case FormPanelType.new:
      case FormPanelType.assign:
        roleDefinitionNameField = this._getRoleDefinitionNameField(false);
        roleDefinitionIdField = this._getRoleDefinitionIdField(false);
        principleIdField = this._getPrincipleIdField(false);
        scopeField = this._getScopeField(false);
        conditionField = this._getConditionField(false);
        break;
      case FormPanelType.view:
      default:
        roleDefinitionNameField = this._getRoleDefinitionNameField(true);
        roleDefinitionIdField = this._getRoleDefinitionIdField(true);
        principleIdField = this._getPrincipleIdField(true);
        scopeField = this._getScopeField(true);
        conditionField = this._getConditionField(isEditDisabled);
        createAndUpdateInfo = this._getCreateAndUpdateInfoField();
        break;
    }

    let GraphInfoField =
      isNullOrUndefined(values.principalId) || values.principalId === ''
        ? null
        : this._renderGraphInfoField();

    let basicPropertiesTab: React.ReactNode = (
      <React.Fragment>
        {roleAssignmentIdField}
        {roleDefinitionNameField}
        {roleDefinitionIdField}
        {GraphInfoField}
        {principleIdField}
        {scopeField}
        {conditionField}
        {createAndUpdateInfo}
      </React.Fragment>
    );
    return basicPropertiesTab;
  }

  private _getRoleAssignmentIdField = (): React.ReactNode => {
    return (
      <Field
        name="id"
        label="Assignment Id"
        ariaLabel="Assignment Id"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        required={true}
        disabled={true}
        component={FormTextField}
      />
    );
  }

  private _getRoleDefinitionNameField = (
    isEditDisabled: boolean
  ): React.ReactNode => {
    const { values } = this.props.formikProps;
    return (
      <Field
        name="roleDefinitionName"
        required={false}
        // tslint:disable-next-line:no-any
        render={(fieldProps: any) => {
          return (
            <FormikDropdown
              {...fieldProps}
              label="Role Definition Name"
              placeholder="Select an Role Definition Name"
              ariaLabel="Role Definition Name Dropdown"
              className={css(
                'Role-Definition-Form-Item',
                detailPanelStyles.formItem
              )}
              defaultSelectedKey={values.roleDefinitionId}
              disabled={isEditDisabled}
              options={this._getRoleDefinitionOptions()}
              onChange={(
                _event: React.FormEvent<HTMLDivElement>,
                option?: IDropdownOption
              ) =>
                this._roleDefinitionNameFieldOnChange(
                  fieldProps,
                  _event,
                  option
                )
              }
            />
          );
        }}
      />
    );
  }

  private _roleDefinitionNameFieldOnChange = (
    // tslint:disable-next-line:no-any
    fieldProps: any,
    _event: React.FormEvent<HTMLDivElement>,
    option?: IDropdownOption
  ) => {
    fieldProps.form.setFieldValue(fieldProps.field.name, option!.key);
    fieldProps.form.setFieldValue('roleDefinitionId', option!.key);
    let roleDefinitionInfo = EMPTY_ROLE_DEFINITION_WITH_LOADING;
    let roleDefinitionIndex = this.props.listRoleDefinitions.hashIndexRDs[
      option!.key
    ];
    roleDefinitionInfo = this.props.listRoleDefinitions.roleDefinitions[
      roleDefinitionIndex
    ] as ISelectedRoleDefinition;
    fieldProps.form.setFieldValue('roleDefinition', roleDefinitionInfo);
    // When select RD from dropdown, pre-populate the first scope for RA
    fieldProps.form.setFieldValue('scope', roleDefinitionInfo.scopes[0]);
  }

  private _getRoleDefinitionIdField = (
    isEditDisabled: boolean
  ): React.ReactNode => {
    return (
      <Field
        name="roleDefinitionId"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        // tslint:disable-next-line:no-any
        render={(fieldProps: any) => {
          return (
            <FormTextField
              {...fieldProps}
              label="Role Definition Id"
              ariaLabel="Role Definition Id"
              className={css(
                'Role-Definition-Form-Item',
                detailPanelStyles.formItem
              )}
              required={true}
              disabled={isEditDisabled}
              onChange={(
                _event: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>,
                newValue?: string | undefined
              ) =>
                this._roleDefinitionIdFieldOnChange(
                  fieldProps,
                  _event,
                  newValue
                )
              }
            />
          );
        }}
      />
    );
  }

  private _roleDefinitionIdFieldOnChange = (
    // tslint:disable-next-line:no-any
    fieldProps: any,
    _event: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>,
    newValue?: string | undefined
  ) => {
    fieldProps.form.setFieldValue(fieldProps.field.name, newValue);
    fieldProps.form.setFieldValue('roleDefinitionName', newValue);
  }

  private _getPrincipleIdField = (isEditDisabled: boolean): ReactNode => {
    let principleIdField = (
      <React.Fragment>
        <Field
          name="principalId"
          // tslint:disable-next-line:no-any
          render={(fieldProps: any) => {
            let listGraphObjects = this.props.listGraphObjects;
            return (
              <FormTextField
                {...fieldProps}
                label="Principal Id"
                ariaLabel="Principal Id"
                className={css(
                  'Role-Definition-Form-Item',
                  raPanelStyles.principalIdFormItem
                )}
                required={true}
                disabled={isEditDisabled}
                onChange={(
                  _event: React.FormEvent<
                    HTMLInputElement | HTMLTextAreaElement
                  >,
                  newValue?: string | undefined
                ) =>
                  this._principleIdFieldOnChange(
                    fieldProps,
                    listGraphObjects,
                    _event,
                    newValue
                  )
                }
              />
            );
          }}
        />
        <IconButton
          iconProps={{ iconName: 'ProfileSearch' }}
          styles={STYLES_SEARCH_GRAPH_ICON}
          title="ProfileSearch"
          text="ProfileSearch"
          ariaLabel="ProfileSearch"
          onClick={(
            event: React.MouseEvent<
              | HTMLAnchorElement
              | HTMLButtonElement
              | HTMLDivElement
              | BaseButton
              | Button
            >
          ) => this._principleIdIconClick(isEditDisabled, event)}
          className={css(
            'Role-Definition-Search-Button',
            raPanelStyles.searchFormIconButton
          )}
        />
        {this.state.showModal ? (
          <div
            className={css(
              'Role-Definition-Form-Item',
              raPanelStyles.searchGraphComponentItem
            )}
          >
            <ConnectedContentSearchObjs
              selectObject={(obj?: IGraphObjectInfo) => this._selectObject(obj)}
              contentSearchObjsType={ContentSearchObjsType.panel}
            />
            {/* <div>hihihihi</div> */}
          </div>
        ) : null}
      </React.Fragment>
    );
    return principleIdField;
  }

  private _principleIdIconClick = (
    isEditDisabled: boolean,
    _event: React.MouseEvent<
      | HTMLAnchorElement
      | HTMLButtonElement
      | HTMLDivElement
      | BaseButton
      | Button
    >
  ) => {
    if (
      !isEditDisabled &&
      this.props.roleAssignmentPanelType !== FormPanelType.edit
    ) {
      this._toggleSearchGraph();
    }
  }

  private _principleIdFieldOnChange = (
    // tslint:disable-next-line:no-any
    fieldProps: any,
    listGraphObjects: IListGraphObjsState,
    _event: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>,
    newValue?: string | undefined
  ) => {
    if (newValue!.indexOf(';') !== -1) {
      this.props.getBulkGraphInfos({ bulkObjectKeyWords: newValue as string });
    }
    if (
      isNullOrUndefined(
        listGraphObjects.bufferGraphObjects!.hashIndexObjs[newValue!]
      )
    ) {
      this.props.getGraphObjectList([
        { objectId: newValue! }
      ] as IGraphObjectInfo[]);
      fieldProps.form.setFieldValue(fieldProps.field.name, newValue);
    } else {
      this._selectObject(
        listGraphObjects.bufferGraphObjects.lstGraphObjects[
          listGraphObjects.bufferGraphObjects!.hashIndexObjs[newValue!]
        ]
      );
    }
  }

  private _getScopeField = (isEditDisabled: boolean): ReactNode => {
    return (
      <Field
        name="scope"
        label="Scope"
        ariaLabel="Scope"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        required={true}
        disabled={isEditDisabled}
        component={FormTextField}
      />
    );
  }

  private _getConditionField = (isEditDisabled: boolean): ReactNode => {
    return (
      <Field
        name="condition"
        label="Condition"
        ariaLabel="Condition"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        required={false}
        disabled={isEditDisabled}
        component={FormTextField}
        multiline
        autoAdjustHeight
      />
    );
  }

  private _getCreateAndUpdateInfoField = () => {
    const { values } = this.props.formikProps;
    return (
      <React.Fragment>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Created By')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {!isNullOrUndefined(values.createdByPrincipal) ? (
              <div>
                {values.createdByPrincipal.displayName} [{values.createdBy}]
              </div>
            ) : (
              values.createdBy
            )}
          </div>
        </div>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Created On')}
            {'(UTC)'}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {values.createdOn}
          </div>
        </div>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Updated By')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {!isNullOrUndefined(values.updatedByPrincipal) ? (
              <div>
                {values.updatedByPrincipal.displayName} [{values.updatedBy}]
              </div>
            ) : (
              values.updatedBy
            )}
          </div>
        </div>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Updated On')}
            {'(UTC)'}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {values.updatedOn}
          </div>
        </div>
      </React.Fragment>
    );
  }

  private _toggleSearchGraph = () => {
    this.setState({ showModal: !this.state.showModal });
  }

  private _addCreateUpdateByPrincipal = (
    roleAssignment: ISelectedRoleAssignment,
    bufferGraphObjects: IBufferGraphObjects
  ): RoleAssignmentFormValue => {
    let roleAssignmentFormValue: RoleAssignmentFormValue = new RoleAssignmentFormValue(
      roleAssignment
    );

    if (isNullOrUndefined(roleAssignmentFormValue.graphObjectInfo)) {
      roleAssignmentFormValue.graphObjectInfo = getObjInfoFromBuffer(
        roleAssignment.principalId,
        bufferGraphObjects
      )!;
    }

    let graphObjectIds: IGraphObjectInfo[] = [];
    if (isGuid(roleAssignment.createdBy)) {
      roleAssignmentFormValue.createdByPrincipal = getObjInfoFromBuffer(
        roleAssignment.createdBy!,
        bufferGraphObjects
      );
      if (
        isNullOrUndefined(roleAssignmentFormValue.createdByPrincipal) &&
        isGuid(roleAssignment.createdBy)
      ) {
        graphObjectIds.push({
          objectId: roleAssignment.createdBy
        } as IGraphObjectInfo);
      }
    }

    if (isGuid(roleAssignment.updatedBy)) {
      roleAssignmentFormValue.updatedByPrincipal = getObjInfoFromBuffer(
        roleAssignment.updatedBy!,
        bufferGraphObjects
      );
      if (
        isNullOrUndefined(roleAssignmentFormValue.updatedByPrincipal) &&
        isGuid(roleAssignment.updatedBy)
      ) {
        graphObjectIds.push({
          objectId: roleAssignment.updatedBy
        } as IGraphObjectInfo);
      }
    }

    if (graphObjectIds.length > 0) {
      this.props.getGraphObjectList(graphObjectIds);
    }

    return roleAssignmentFormValue;
  }

  private _updateCreateUpdateByPrincipal = (
    formikPropsRA: FormikProps<RoleAssignmentFormValue>,
    bufferGraphObjects: IBufferGraphObjects
  ) => {
    const curRoleAssignmentFormValue = formikPropsRA.values;
    const roleAssignmentFormValue: RoleAssignmentFormValue = this._addCreateUpdateByPrincipal(
      curRoleAssignmentFormValue,
      bufferGraphObjects
    );
    if (
      curRoleAssignmentFormValue.createdByPrincipal !==
        roleAssignmentFormValue.createdByPrincipal ||
      curRoleAssignmentFormValue.updatedByPrincipal !==
        roleAssignmentFormValue.updatedByPrincipal
    ) {
      formikPropsRA.setFieldValue(
        'createdByPrincipal',
        roleAssignmentFormValue.createdByPrincipal
      );
      formikPropsRA.setFieldValue(
        'updatedByPrincipal',
        roleAssignmentFormValue.updatedByPrincipal
      );
    }
    return roleAssignmentFormValue;
  }

  private _selectObject = (obj?: IGraphObjectInfo) => {
    const { setFieldValue } = this.props.formikProps;
    this.setState({
      showModal: false,
      selectedGraphObject: obj!
    });
    if (isNullOrUndefined(obj)) {
      return;
    }
    setFieldValue('principalId', obj!.objectId);
    if (obj!.objectId) {
      setFieldValue('graphObjectInfo.objectType', obj!.objectType);
      setFieldValue('graphObjectInfo.displayName', obj!.displayName);
      setFieldValue(
        'graphObjectInfo.userPrincipalName',
        obj!.userPrincipalName
      );
      setFieldValue('graphObjectInfo.mail', obj!.mail);
      setFieldValue('graphObjectInfo.homepage', obj!.homepage);
    }
  }

  private _getRoleDefinitionOptions = (): IDropdownOption[] => {
    let options = [] as IDropdownOption[];
    const { roleDefinitions } = this.props.listRoleDefinitions;
    for (let i = 0; i < roleDefinitions.length; i++) {
      let rd = roleDefinitions[i];
      let option = {
        key: rd.id,
        text: rd.name
      };
      options.push(option);
    }
    return options;
  }

  private _renderGraphInfoField = () => {
    let { values } = this.props.formikProps;
    return (
      <React.Fragment>
        <Field
          name="graphObjectInfo.objectType"
          label="Principal Type"
          ariaLabel="Principal Type"
          className={css(
            'Role-Definition-Form-Item',
            detailPanelStyles.formItem
          )}
          required={false}
          disabled={true}
          component={FormTextField}
        />
        <Field
          name="graphObjectInfo.displayName"
          label="Name"
          ariaLabel="Name"
          className={css(
            'Role-Definition-Form-Item',
            detailPanelStyles.formItem
          )}
          required={false}
          disabled={true}
          component={FormTextField}
        />
        {values.graphObjectInfo.objectType === GraphObjectTypes.User ? (
          <Field
            name="graphObjectInfo.userPrincipalName"
            label="User Principal Name"
            ariaLabel="User Principal Name"
            className={css(
              'Role-Definition-Form-Item',
              detailPanelStyles.formItem
            )}
            type="graphObjectInfo.displayName"
            required={false}
            disabled={true}
            component={FormTextField}
          />
        ) : (
          undefined
        )}
        {values.graphObjectInfo.objectType !==
        GraphObjectTypes.ServicePrincipal ? (
          <Field
            name="graphObjectInfo.mail"
            label="Email"
            ariaLabel="Email"
            className={css(
              'Role-Definition-Form-Item',
              detailPanelStyles.formItem
            )}
            required={false}
            disabled={true}
            component={FormTextField}
          />
        ) : (
          undefined
        )}
        {values.graphObjectInfo.objectType ===
        GraphObjectTypes.ServicePrincipal ? (
          <Field
            name="graphObjectInfo.homepage"
            label="Home Page"
            ariaLabel="Home Page"
            className={css(
              'Role-Definition-Form-Item',
              detailPanelStyles.formItem
            )}
            required={false}
            disabled={true}
            component={FormTextField}
          />
        ) : (
          undefined
        )}
      </React.Fragment>
    );
  }
}

export const RoleAssignmentForm = translate('Role Definition Form')(
  RoleAssignmentFormComponent
);



