import { mergeStyles } from '@uifabric/styling';

export const CUSTOMIZED_PANEL_STYLE = {
  root: mergeStyles({
    top: 50 // 50px is APP_BAR_HEIGHT from MyStar
  }),
  navigation: mergeStyles({
    height: 30
  }),
  header: mergeStyles({
    marginTop: '0px',
    marginBottom: '20px'
  }),
  content: mergeStyles({
    paddingLeft: '0 !important',
    paddingRight: '0 !important'
  })
};



