import { LoadingState } from '@microsoft/portal-app/lib/models/ILoading';
import { css, Label } from 'office-ui-fabric-react';
import { DefaultButton } from 'office-ui-fabric-react/lib/Button';
import {
  CheckboxVisibility,
  DetailsList,
  DetailsListLayoutMode,
  IColumn,
  IDetailsRowProps
} from 'office-ui-fabric-react/lib/DetailsList';
import { Spinner, SpinnerSize } from 'office-ui-fabric-react/lib/Spinner';
import { ResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { isNullOrUndefined, isUndefined } from 'util';
import {
  COL_AAD_LIST_ROLE_ASSIGNS,
  EmptyFilterSearchRA,
  emptyTblListRAs,
  IDisplayItemRoleAssignment,
  IFilterSearchRA,
  IRoleAssignment,
  ITableListRoleAssignments
} from '../../../models/aadPAP/roleAssignment';
import { ISelectedRoleAssignment } from '../../../models/aadPAP/roleAssignment/ISelectedRoleAssignment';
import { GraphObjectTypes } from '../../../models/graph';
import { STYLE_LINK_DETAILS_LIST } from '../../../Styles/STYLE_LINK_DETAILS_LIST';
import { STYLE_USER_SELECT_ALL } from '../../../Styles/STYLE_USER_SELECT_ALL';
import { transformRoleAssignmentsToGraphObjects } from '../../../utilities/actionsUtilities';
import { renderEveryOtherRowRoleAssignment } from '../../../utilities/fabric';
import { deepClone } from '../../../utilities/object';
import { CustomSelection } from '../../Common/CustomSelection/CustomSelection';
import Loading from '../../Common/Loading/Loading';
import { FormPanelType } from '../../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import {
  IRoleAssignmentsTableProps,
  IRoleAssignmentsTableState
} from './RoleAssignmentsTable.types';
import { getTableDisplayRoleAssignmentColumns } from './RoleAssingmentsTable.columns';
const styles = require('./RoleAssignmentsTable.scoped.scss');

export class RoleAssignmentsTable extends React.Component<
  IRoleAssignmentsTableProps,
  IRoleAssignmentsTableState
> {
  private _selection: CustomSelection;
  constructor(props: IRoleAssignmentsTableProps) {
    super(props);
    let initTableListRoleAssignments: ITableListRoleAssignments = {
      ...emptyTblListRAs
    };

    if (
      !isNullOrUndefined(this.props.listRoleAssignments.roleAssignments) &&
      this.props.listRoleAssignments.currentAppId ===
        this.props.currentNpd.appId
    ) {
      let displayRoleAssignmentList = deepClone(
        this.getTblListRAs(
          this.props.listRoleAssignments
            .roleAssignments as IDisplayItemRoleAssignment[],
          this.props
        )
      );
      initTableListRoleAssignments = {
        appId: this.props.listRoleAssignments.currentAppId,
        displayRoleAssignmentList: displayRoleAssignmentList
      } as ITableListRoleAssignments;
    }

    this.state = {
      tableListRoleAssignments: initTableListRoleAssignments,
      columns: COL_AAD_LIST_ROLE_ASSIGNS,
      showPanel: false,
      panelType: FormPanelType.view,
      selectedRoleAssignment: {} as ISelectedRoleAssignment,
      announce: ''
    };

    // do you really need to put this in constructor???
    this._selection = new CustomSelection({
      onSelectionChanged: () => {
        const selectionItems = this._getSelectionDetails();
        props.setSingleSelectedRoleAssignment(selectionItems[0]);
        this.setState({
          selectedRoleAssignment: selectionItems[0]
        });
        if (this.props.setSelectedRoleAssignments) {
          this.props.setSelectedRoleAssignments([
            ...(this._selection.getSelection() as IDisplayItemRoleAssignment[])
          ]);
        }
      },
      onAnnounce: (info: string) => {
        this.setState({ announce: info });
      }
    });
  }

  public componentWillReceiveProps(nextProps: IRoleAssignmentsTableProps) {
    if (
      !isNullOrUndefined(nextProps.currentNpd) &&
      nextProps.currentNpd.appId !==
        this.props.listRoleAssignments.currentAppId &&
      !nextProps.listRoleAssignments.isLoading
    ) {
      this.getNewListRAs(nextProps.currentNpd.appId);
    }
    const {
      bulkDeletionComplete: currentBulkDeletionComplete
    } = this.props.listRoleAssignments;
    const {
      bulkDeletionComplete: nextBulkDeletionComplete
    } = nextProps.listRoleAssignments;

    if (
      (this.props.listRoleDefinitions.isLoading &&
        !nextProps.listRoleDefinitions.isLoading) ||
      (this.props.listGraphObjects.isLoading &&
        !nextProps.listGraphObjects.isLoading)
    ) {
      this.flushTblListRAs(
        this.state.tableListRoleAssignments.displayRoleAssignmentList,
        nextProps
      );
    }

    if (
      (this.props.listRoleAssignments.isLoading &&
        !nextProps.listRoleAssignments.isLoading) ||
      (this.props.selectedRoleAssignment.isLoading &&
        !nextProps.selectedRoleAssignment.isLoading) ||
      (currentBulkDeletionComplete === false && nextBulkDeletionComplete)
    ) {
      if (
        this.props.listRoleDefinitions.currentAppId !==
          nextProps.listRoleAssignments.currentAppId ||
        this.props.listRoleDefinitions.loadingState !== LoadingState.loaded
      ) {
        this.getListRDs(nextProps.currentNpd.appId);
      }

      let graphObjectIds = transformRoleAssignmentsToGraphObjects(
        nextProps.listRoleAssignments.roleAssignments,
        nextProps.listGraphObjects
      );
      this.props.getListGraphObjects(graphObjectIds);

      this.flushTblListRAs(
        nextProps.listRoleAssignments
          .roleAssignments as IDisplayItemRoleAssignment[],
        nextProps
      );
    }

    if (nextProps.textFilter !== this.props.textFilter) {
      this._onFilter(nextProps.textFilter);
    }

    const {
      selectedRoleAssignments: nextSelectedRoleAssignments,
      roleAssignments: nextRoleAssignments
    } = nextProps.listRoleAssignments;

    const {
      roleAssignments: currentRoleAssignments
    } = this.props.listRoleAssignments;

    // compare  Role Assignments by ref
    if (currentRoleAssignments !== nextRoleAssignments) {
      this._getListItemsToSelect(
        nextSelectedRoleAssignments,
        nextRoleAssignments
      );
    }
  }

  public render() {
    const { t, isSmallTable } = this.props;
    // const { displayRoleAssignmentList } = this.state.tableListRoleAssignments!;
    let responsiveMode = this._getResponsiveMode();
    if (isSmallTable) {
      responsiveMode = ResponsiveMode.small;
    }
    // const numberOfResult = displayRoleAssignmentList
    //   ? displayRoleAssignmentList.length
    //   : 0;
    let columns = getTableDisplayRoleAssignmentColumns(responsiveMode, t);
    if (
      !this.props.listRoleAssignments.isLoading &&
      this.props.listRoleAssignments.loadingState === LoadingState.loaded
    ) {
      return (
        <div className="RA-DetailsList">
          {/* <Announced
            id={(
              Math.floor(Math.random() * (numberOfResult + 1000)) + 1
            ).toString()}
            message={`${numberOfResult} items found`}
          /> */}
          <div
            className={css(styles.visuallyhidden)}
            role="status"
            aria-live="polite"
            aria-atomic="true"
          >
            {this.state.announce}
          </div>
          <DetailsList
            items={
              this.state.tableListRoleAssignments.displayRoleAssignmentList
            }
            setKey={'id'}
            columns={columns}
            className={css(styles.listRoleAssignments)}
            layoutMode={DetailsListLayoutMode.justified}
            selection={this._selection}
            selectionPreservedOnEmptyClick={true}
            ariaLabelForGrid={this.props.t('Role Assignment List')}
            ariaLabelForSelectionColumn="Toggle role assignment selection"
            ariaLabelForSelectAllCheckbox="Toggle selection for all role assignment"
            checkboxVisibility={
              this.props.isSmallTable
                ? CheckboxVisibility.hidden
                : CheckboxVisibility.onHover
            }
            //  checkboxVisibility={CheckboxVisibility.hidden}
            onRenderItemColumn={this._renderItemColumn}
            onRenderRow={(props: IDetailsRowProps | undefined) =>
              renderEveryOtherRowRoleAssignment(
                props,
                this.props.setShowPanel!,
                this._selection
              )
            }
            // onColumnHeaderClick={this.onColumnClick}
          />
          {this.props.listRoleAssignments.isByPage ? (
            <div className={css('pagination', styles.pagination)}>
              <DefaultButton
                data-automation-id="pagination"
                allowDisabledFocus={true}
                disabled={false}
                text="First Page"
                onClick={(event: React.MouseEvent<HTMLDivElement>) => {
                  event.stopPropagation();
                  this.getFirstPage();
                }}
              />
              <Label>{`  ${this.props.listRoleAssignments.iPageNum}  `}</Label>
              <DefaultButton
                data-automation-id="pagination"
                allowDisabledFocus={true}
                disabled={false}
                text="Next Page"
                onClick={this.getNextPage}
              />
            </div>
          ) : null}
        </div>
      );
    } else {
      return isSmallTable ? <Loading isSmallWindow={true} /> : <Loading />;
    }
  }

  public refresh = () => {
    if (this.props.currentNpd) {
      const appId = this.props.currentNpd.appId;
      this.getNewListRAs(appId);
      this.flushTblListRAs(
        this.state.tableListRoleAssignments.displayRoleAssignmentList,
        this.props
      );
    }
  }
  private _getListItemsToSelect = (
    nextSelectedRoleAssignments: IDisplayItemRoleAssignment[] = [],
    nextRoleAssignments: IRoleAssignment[] = []
  ): void => {
    this._selection.setChangeEvents(false);
    this._selection.setAllSelected(false);
    let flag = false;
    let correctedSelectedRAs = [...nextSelectedRoleAssignments];
    nextRoleAssignments.forEach((ra: IRoleAssignment, idx: number) => {
      const index = nextSelectedRoleAssignments.findIndex(
        selectedRa => ra.id === selectedRa.id
      );
      if (index !== -1) {
        this._selection.setIndexSelected(idx, true, false);
      } else {
        flag = true;
        correctedSelectedRAs = correctedSelectedRAs.splice(index, 1);
      }
      if (flag) {
        this.props.setSelectedRoleAssignments(correctedSelectedRAs);
      }
    });
    this._selection.setChangeEvents(true);
  }
  private _onFilter = (text: string): void => {
    let itemListRAs = this.getTblListRAs(
      this.props.listRoleAssignments
        .roleAssignments as IDisplayItemRoleAssignment[],
      this.props
    );
    const items = text
      ? itemListRAs.filter(i => {
          return (
            i.scope.toLowerCase().indexOf(text.toLowerCase()) > -1 ||
            i.nameRD.toLowerCase().indexOf(text.toLowerCase()) > -1 ||
            (i.nameObj
              ? i.nameObj.toLowerCase().indexOf(text.toLowerCase()) > -1
              : false)
          );
        })
      : itemListRAs;
    const filterTbl: ITableListRoleAssignments = {
      appId: this.props.currentNpd.appId,
      displayRoleAssignmentList: items as IDisplayItemRoleAssignment[]
    };

    this.setState({
      tableListRoleAssignments: filterTbl
    });
  }

  private _getSelectionDetails(): ISelectedRoleAssignment[] {
    const selectionItems = this._selection.getSelection();
    // if (isObjectEquivalent(selectionItems[0]!, this.state.selectedRoleAssignment!)) {
    //   // selectionItems.push({} as IObjectWithKey);
    // }
    return selectionItems as ISelectedRoleAssignment[];
  }

  private _getResponsiveMode = (): ResponsiveMode => {
    let { responsiveMode } = this.props;
    if (responsiveMode === undefined) {
      responsiveMode = ResponsiveMode.large;
    }
    return responsiveMode;
  }

  private getListRDs = (appId: string) => {
    if (!isNullOrUndefined(appId) || appId === '') {
      this.props.getListRDs(appId);
    }
  }

  private getNewListRAs = (appId: string, filter?: IFilterSearchRA) => {
    this.props.getListRAs(
      appId,
      isUndefined(filter) ? { ...EmptyFilterSearchRA } : { ...filter },
      undefined
    );
  }

  private getListRAs = (
    appId: string,
    filterSearchRA: IFilterSearchRA,
    skipToken?: string
  ) => {
    this.props.getListRAs(appId, filterSearchRA, skipToken);
  }

  private getFirstPage = () => {
    if (this.props.listRoleAssignments.isLoading) {
      return;
    }
    this.getListRAs(
      this.props.currentNpd.appId,
      this.props.listRoleAssignments.filterSearchRA
    );
  }

  private getNextPage = () => {
    if (
      this.props.listRoleAssignments.skipToken === null ||
      this.props.listRoleAssignments.isLoading
    ) {
      return;
    }
    this.getListRAs(
      this.props.currentNpd.appId,
      this.props.listRoleAssignments.filterSearchRA,
      this.props.listRoleAssignments.skipToken
    );
  }

  // private _onClickRoleID = (index: number) => {
  //   this.props.setSingleSelectedRoleAssignment(
  //     this.state.tableListRoleAssignments.displayRoleAssignmentList[index]
  //   );
  //   if (!isUndefined(this.props.setShowPanel)) {
  //     this.props.setShowPanel(true, FormPanelType.view);
  //   }
  // this.props.setShowPanel!(true, FormPanelType.view);
  // }

  private _renderItemColumn = (
    item: IDisplayItemRoleAssignment,
    _index: number | undefined,
    column: IColumn | undefined
  ) => {
    switch (column!.key) {
      case 'id':
        return (
          <b
            //   onClick={() => this.onClickRoleID(index!)}
            style={STYLE_LINK_DETAILS_LIST}
          >
            {item[column!.fieldName!]}
          </b>
        );
      case 'nameRD':
        return (
          <span style={STYLE_USER_SELECT_ALL}>
            {this.props.listRoleDefinitions.loadingState ===
            LoadingState.loaded ? (
              item.nameRD
            ) : this.props.listRoleDefinitions.isLoading ? (
              <Spinner size={SpinnerSize.xSmall} />
            ) : (
              <React.Fragment />
            )}
          </span>
        );
      case 'nameObj': {
        return (
          <span style={STYLE_USER_SELECT_ALL}>
            {!isNullOrUndefined(item.graphObjectInfo) ? (
              <React.Fragment>
                {item.graphObjectInfo.objectType === GraphObjectTypes.Group ? (
                  <i className="fa fa-fw fa-group" aria-label="Group" />
                ) : (
                  <React.Fragment />
                )}
                {item.graphObjectInfo.objectType === GraphObjectTypes.User ? (
                  <i className="fa fa-fw fa-user" aria-label="User" />
                ) : (
                  <React.Fragment />
                )}
                {item.graphObjectInfo.objectType ===
                GraphObjectTypes.ServicePrincipal ? (
                  <i
                    className="fa fa-fw fa-globe"
                    aria-label="Service Principal"
                  />
                ) : (
                  <React.Fragment />
                )}
                {item.nameObj}
              </React.Fragment>
            ) : this.props.listGraphObjects.isLoading ? (
              <Spinner size={SpinnerSize.xSmall} />
            ) : (
              <React.Fragment />
            )}
          </span>
        );
      }
      default:
        return (
          <span style={STYLE_USER_SELECT_ALL}>
            {' '}
            {
              item[
                column!.fieldName as keyof IDisplayItemRoleAssignment
              ] as string
            }
          </span>
        );
    }
  }

  private getTblListRAs = (
    lstRAs: IDisplayItemRoleAssignment[],
    nextProps: IRoleAssignmentsTableProps
  ): IDisplayItemRoleAssignment[] => {
    let itemListRAs = [...lstRAs];
    for (let i = 0; i < itemListRAs.length; i++) {
      if (
        itemListRAs[i].nameRD === undefined &&
        nextProps.listRoleDefinitions.loadingState === LoadingState.loaded &&
        nextProps.listRoleDefinitions.roleDefinitions.length > 0
      ) {
        itemListRAs[i].roleDefinition =
          nextProps.listRoleDefinitions.roleDefinitions[
            nextProps.listRoleDefinitions.hashIndexRDs[
              itemListRAs[i].roleDefinitionId
            ]
          ];
        if (!isNullOrUndefined(itemListRAs[i].roleDefinition)) {
          itemListRAs[i].nameRD = itemListRAs[i].roleDefinition.name;
        }
      }
      if (
        itemListRAs[i].nameObj === undefined &&
        nextProps.listGraphObjects.loadingState === LoadingState.loaded &&
        nextProps.listGraphObjects.bufferGraphObjects.lstGraphObjects.length >
          0 &&
        nextProps.listGraphObjects.bufferGraphObjects.hashIndexObjs[
          itemListRAs[i].principalId
        ] !== undefined
      ) {
        itemListRAs[i].graphObjectInfo =
          nextProps.listGraphObjects.bufferGraphObjects.lstGraphObjects[
            nextProps.listGraphObjects.bufferGraphObjects.hashIndexObjs[
              itemListRAs[i].principalId
            ]
          ];
        itemListRAs[i].nameObj = itemListRAs[i].graphObjectInfo
          .displayName as string;
      }
    }
    return itemListRAs;
  }

  private flushTblListRAs = (
    itemListRAs: IDisplayItemRoleAssignment[],
    nextProps: IRoleAssignmentsTableProps
  ) => {
    let updItemListRAs = deepClone(this.getTblListRAs(itemListRAs, nextProps));
    this.setState({
      tableListRoleAssignments: {
        appId: this.state.tableListRoleAssignments.appId,
        displayRoleAssignmentList: updItemListRAs
      } as ITableListRoleAssignments
    });
    // this._selection.setItems(updItemListRAs);
  }
}



