import { mergeStyles } from '@uifabric/styling';
import { memoizeFunction } from '@uifabric/utilities';

const APP_BAR_HEIGHT = '50px';

export interface IRoleAssignmentPanelClassNames {
  root: string;
  headerNavigation: string;
  header: string;
  headerText: string;
}

export const getClassNames = memoizeFunction(
  (isContentFullBleed: boolean = false): IRoleAssignmentPanelClassNames => {
    return {
      root: mergeStyles({
        top: APP_BAR_HEIGHT + ' !important', // TODO: Figure out specificity issue
        selectors: {
          '.ms-Panel-content': isContentFullBleed
            ? {
                padding: 0
              }
            : null
        }
      }),
      header: mergeStyles({
        display: 'flex'
      }),
      headerText: mergeStyles({
        flex: '1'
      }),
      headerNavigation: mergeStyles({
        justifyContent: 'flex-end'
      })
    };
  }
);



