import { isNullOrUndefined } from 'util';
import { IGraphObjectInfo } from '../../../models/graph';
import { IBufferGraphObjects } from '../../../models/graph/IBufferGraphObjects';
import { isGuid } from '../../../utilities/guid';

export const getObjInfoFromBuffer = (
  principalId: string,
  bufferGraphObjects: IBufferGraphObjects
): IGraphObjectInfo | undefined => {
  let objInfo: IGraphObjectInfo | undefined;
  if (
    isGuid(principalId) &&
    !isNullOrUndefined(bufferGraphObjects) &&
    !isNullOrUndefined(bufferGraphObjects.hashIndexObjs) &&
    !isNullOrUndefined(bufferGraphObjects.hashIndexObjs[principalId])
  ) {
    objInfo =
      bufferGraphObjects.lstGraphObjects[
        bufferGraphObjects.hashIndexObjs[principalId]
      ];
  }
  return objInfo;
};



