import { IStyleFunctionOrObject } from '@uifabric/utilities';
import {
  IChoiceGroupOptionStyleProps,
  IChoiceGroupOptionStyles
} from 'office-ui-fabric-react';
import { IColumn } from 'office-ui-fabric-react/lib/DetailsList';
import { IWithResponsiveModeState } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { InjectedTranslateProps } from 'react-i18next';
import {
  GraphObjectTypes,
  IGraphObjectInfo,
  ISearchGraphObjectsInfo
} from '../../../models/graph';

/**
 * The props for the ListPage control.
 */
export interface IContentSearchObjsProps
  extends IContentSearchObjsActions,
    IContentSearchObjsMappedProps,
    IConnectedContentSearchObjsProps,
    React.Props<IContentSearchObjs>,
    InjectedTranslateProps,
    IWithResponsiveModeState {}

/**
 * Represents the ListPage control's internal state
 */
export interface IContentSearchObjsState {
  objType: GraphObjectTypes;
  keyWords: string;
  tblSearchGraphObjectsInfo: ISearchGraphObjectsInfo;
  columns: IColumn[];
}

/**
 * Used as a standalone interface by mapStateToProps, the IListPageMappedProps defines the non action properties.
 * for the ListPage control.
 */
export interface IContentSearchObjsMappedProps {
  searchGraphObjectsInfo: ISearchGraphObjectsInfo;
}

/**
 * Used as a standalone interface by mapDispatchToProps, the IListPageActions interface defines the actions
 * for the ListPage control
 */
export interface IContentSearchObjsActions {
  searchGraphInfo(payload: {
    objectType: GraphObjectTypes;
    keyWords: string;
  }): void;
}

/**
 * Used by the React.Props interface, IListPage represents the interface for the ListPage when used as a ref.
 */
export interface IContentSearchObjs {}

/**
 * The props for the redux connected ListPage
 * @description Called "own" props in the redux function mapStateToProps.
 * The mapStateToProps can map the properties of this interface to the IListPageProps interface.
 */
export interface IConnectedContentSearchObjsProps {
  // isSmallList?: boolean;
  isInPanel?: boolean;
  contentSearchObjsType?: ContentSearchObjsType;
  selectObject(obj?: IGraphObjectInfo): void;
}

export const optionStyles: IStyleFunctionOrObject<
  IChoiceGroupOptionStyleProps,
  IChoiceGroupOptionStyles
> = {
  root: { marginRight: '10px' },
  labelWrapper: { maxWidth: '80px!important' }
};

export const optionPanelStyles: IStyleFunctionOrObject<
  IChoiceGroupOptionStyleProps,
  IChoiceGroupOptionStyles
> = {
  root: { marginRight: '10px' },
  labelWrapper: { width: '10px!important' }
};

export enum ContentSearchObjsType {
  ssm = 'ssm',
  panel = 'panel'
}



