import { css } from '@uifabric/utilities';
import { Spinner, SpinnerSize } from 'office-ui-fabric-react/lib/Spinner';
import * as React from 'react';

const loadingStyles = require('./Loading.scoped.scss');

interface ILoadingProps {
  isSmallWindow?: boolean;
}

export default class Loading extends React.Component<ILoadingProps, {}> {
  // tslint:disable-next-line:no-any
  // private loadingRef = React.createRef<any>();
  // public componentDidMount() {
  //   this._focus();
  // }
  public render(): JSX.Element {
    return (
      // <div tabIndex={-1} ref={this.loadingRef}>
      <Spinner
        className={
          this.props.isSmallWindow
            ? css(loadingStyles.loadingIcon, loadingStyles.loadingSmallWindow)
            : css(loadingStyles.loadingIcon)
        }
        size={SpinnerSize.large}
        label="Loading ..."
        ariaLive="assertive"
        labelPosition="right"
      />
      // </div>
    );
  }
  // private _focus() {
  //   const node = this.loadingRef.current;
  //   if (node) {
  //     node.focus();
  //   }
  // }
}



