import { ILoading } from '@microsoft/portal-app/lib/models/ILoading';
import { newGuid } from '../../../utilities/guid';
import { IGraphObjectInfo } from '../../graph';
import { IRoleDefinition } from '../roleDefinition/IRoleDefinition';
// import { IRoleAssignment } from './IRoleAssignment';
import { IDisplayItemRoleAssignment } from './ITableDisplayRoleAssignment';

// export interface ISelectedRoleAssignment extends IRoleAssignment, ILoading { }

export interface ISelectedRoleAssignment extends IDisplayItemRoleAssignment, ILoading {}

export const getEmptySelectedRoleAssignment = (): ISelectedRoleAssignment => {
  return {
    id: newGuid(),
    roleDefinitionId: '',
    principalId: '',
    scope: '',
    condition: '',
    isBuiltIn: false,
    createdBy: '',
    createdOn: '',
    updatedBy: '',
    updatedOn: '',
    nameRD: '',
    nameObj: '',
    roleDefinition: {} as IRoleDefinition,
    graphObjectInfo: {} as IGraphObjectInfo,
    isLoading: false
  };
};



