import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../../models';
import { IRoleAssignment } from '../../../models/aadPAP/roleAssignment';
import { RoleAssignmentPanel } from './RoleAssignmentPanel';
import {
  IConnectedRoleAssignmentPanelProps,
  IRoleAssignmentPanelActions,
  IRoleAssignmentPanelMappedProps
} from './RoleAssignmentPanel.type';
/**
 * Maps the external (i.e. own) props and the state to the properties of the RoleAssignment control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IRoleAssignmentProps) for the RoleAssignment control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleAssignmentPanelProps
): IRoleAssignmentPanelMappedProps => {
  const { selectedRoleAssignment, listRoleDefinitions, selectedRoleDefinition } = state.app;
  return {
    selectedRoleAssignment,
    listRoleDefinitions,
    selectedRoleDefinition
  };
};

/**
 * Maps the dispatch actions to the props for the RoleAssignment control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IRoleAssignmentProps) for the RoleAssignment control
 */
const mapDispatchToProps = (dispatch: Dispatch<IRootAuthzState>): IRoleAssignmentPanelActions => {
  const updateRoleAssignment = createAction<IRoleAssignment>(AuthzActions.updateRoleAssignment, dispatch);
  const deleteRoleAssignment = createAction<IRoleAssignment>(AuthzActions.deleteRoleAssignment, dispatch);
  return {
    updateRoleAssignment: (RoleAssignment: IRoleAssignment) => updateRoleAssignment(RoleAssignment),
    deleteRoleAssignment: (RoleAssignment: IRoleAssignment) => deleteRoleAssignment(RoleAssignment)
  };
};

/**
 * A redux connected RoleAssignment control.
 * @description Supports translation
 */
export const ConnectedRoleAssignmentPanel = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('Role Assignment Panel')(RoleAssignmentPanel));



