import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../../models';
import {
  IDisplayItemRoleAssignment,
  IFilterSearchRA
} from '../../../models/aadPAP/roleAssignment';
import { IRoleAssignmentQueryParams } from '../../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { IGraphObjectInfo } from '../../../models/graph';
import { RoleAssignmentsTable } from './RoleAssignmentsTable';
import {
  IConnectedRoleAssignmentsTableProps,
  IRoleAssignmentsTableActions,
  IRoleAssignmentsTableMappedProps
} from './RoleAssignmentsTable.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the RoleAssignmentsTable control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IRoleAssignmentsTableProps) for the RoleAssignmentsTable control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleAssignmentsTableProps
): IRoleAssignmentsTableMappedProps => {
  const {
    listRoleDefinitions,
    listRoleAssignments,
    listGraphObjects,
    currentNpd,
    selectedRoleAssignment,
    roleAssignmentQueryParams
  } = state.app;

  return {
    ...state,
    listRoleDefinitions,
    listRoleAssignments,
    listGraphObjects,
    currentNpd,
    selectedRoleAssignment,
    roleAssignmentQueryParams
  };
};

/**
 * Maps the dispatch actions to the props for the RoleAssignmentsTable control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IRoleAssignmentsTableProps) for the RoleAssignmentsTable control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): IRoleAssignmentsTableActions => {
  const getRoleAssignmentListAction = createAction<IRoleAssignmentQueryParams>(
    AuthzActions.getRoleAssignmentList,
    dispatch
  );
  const getListRoleDefinitionsAction = createAction<string>(
    AuthzActions.getRoleDefinitionList,
    dispatch
  );
  const getGraphObjectsAction = createAction<IGraphObjectInfo[]>(
    AuthzActions.getGraphObjectList,
    dispatch
  );
  const setSingleSelectedRoleAssignmentAction = createAction<
    IDisplayItemRoleAssignment
  >(AuthzActions.setSingleSelectedRoleAssignment, dispatch);
  const setSelectedRoleAssignmentsAction = createAction<
    IDisplayItemRoleAssignment[]
  >(AuthzActions.setSelectedRoleAssignments, dispatch);
  return {
    getListRAs: (
      appId: string,
      filterSearchRA: IFilterSearchRA,
      skipToken?: string
    ) => {
      getRoleAssignmentListAction({ appId, filterSearchRA, skipToken });
    },
    getListGraphObjects: (graphObjectIds: IGraphObjectInfo[]) => {
      getGraphObjectsAction(graphObjectIds);
    },
    getListRDs: (appId: string) => {
      getListRoleDefinitionsAction(appId);
    },
    setSingleSelectedRoleAssignment: (
      displayRoleAssignment: IDisplayItemRoleAssignment
    ) => {
      setSingleSelectedRoleAssignmentAction(displayRoleAssignment);
    },
    setSelectedRoleAssignments: (
      displayRoleAssignments: IDisplayItemRoleAssignment[]
    ) => {
      setSelectedRoleAssignmentsAction(displayRoleAssignments);
    }
  };
};

/**
 * A redux connected RoleAssignmentsTable control.
 * @description Supports translation
 */
export const ConnectedRoleAssignmentsTable = connect(
  mapStateToProps,
  mapDispatchToProps,
  null,
  { withRef: true }
)(translate('App', { withRef: true })(RoleAssignmentsTable));



