import { translate } from 'react-i18next';
import { connect } from 'react-redux';
import { SearchObjects } from './SearchObjects';
import { IConnectedSearchObjectsProps, ISearchObjectsActions, ISearchObjectsMappedProps } from './SearchObjects.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the ListPage control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapStateToProps = (
  _ownProps: IConnectedSearchObjectsProps
): ISearchObjectsMappedProps => {
  return {};
};

/**
 * Maps the dispatch actions to the props for the ListPage control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IListPageProps) for the ListPage control
 */
const mapDispatchToProps = (
  // dispatch: Dispatch<IRootAuthzState>
): ISearchObjectsActions => {
  return {};
};

/**
 * A redux connected ListPage control.
 * @description Supports translation
*/
export const ConnectedSearchObjects = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('OneAuthZ')(SearchObjects));


