import { IStyleFunctionOrObject } from '@uifabric/utilities';
import { IPanelStyleProps, IPanelStyles, mergeStyles } from 'office-ui-fabric-react';
import * as React from 'react';
import { InjectedTranslateProps } from 'react-i18next';
import { IApplication } from '../../../models/aadPAP/application/IApplication';
import { IRoleAssignmentQueryParams } from '../../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { IGraphObjectInfo } from '../../../models/graph';
import { FormPanelType } from '../../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';

/**
 * The props for the ListPage control.
 */
export interface IDetailObjectInfoProps
  extends IDetailObjectInfoActions,
    IDetailObjectInfoMappedProps,
    IConnectedDetailObjectInfoProps,
    React.Props<IDetailObjectInfo>,
    InjectedTranslateProps {}

/**
 * Represents the ListPage control's internal state
 */
export interface IDetailObjectInfoState {
  selectedPivot: DetailObjectPivots;
}

/**
 * Used as a standalone interface by mapStateToProps, the IListPageMappedProps defines the non action properties.
 * for the ListPage control.
 */
export interface IDetailObjectInfoMappedProps {
  roleAssignmentQueryParams: IRoleAssignmentQueryParams;
  currentNpd: IApplication;
}

/**
 * Used as a standalone interface by mapDispatchToProps, the IListPageActions interface defines the actions
 * for the ListPage control
 */
export interface IDetailObjectInfoActions {
  getListRAs: (queryRA: IRoleAssignmentQueryParams) => void;
  updateRAQuery: (queryRA: IRoleAssignmentQueryParams) => void;
}

/**
 * Used by the React.Props interface, IListPage represents the interface for the ListPage when used as a ref.
 */
export interface IDetailObjectInfo {}

/**
 * The props for the redux connected ListPage
 * @description Called "own" props in the redux function mapStateToProps.
 * The mapStateToProps can map the properties of this interface to the IListPageProps interface.
 */
export interface IConnectedDetailObjectInfoProps {
  showPanel: boolean;
  curObj: IGraphObjectInfo;
  showRAPanel: (formPanelType: FormPanelType, showPanel: boolean) => void;
  dismissPanel(): void;
}

export const panelStyles: IStyleFunctionOrObject<IPanelStyleProps, IPanelStyles> = {
  root: mergeStyles({
    top: 50
  }),
  navigation: mergeStyles({
    height: 30
  }),
  header: mergeStyles({
    marginTop: '0px',
    marginBottom: '5px'
  })
};

export enum DetailObjectPivots {
  UserDetails = 'User Details',
  RoleAssignments = 'Role Assignments'
}



