import { css } from '@uifabric/utilities';
import * as React from 'react';
import { isUndefined } from 'util';
import { IGraphObjectInfo } from '../../models/graph';
import { ConnectedRoleAssignmentPanel } from '../RoleAssignment/RoleAssignmentPanel/ConnectedRoleAssignmentPanel';
import { FormPanelType } from '../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import { ConnectedContentSearchObjs } from './ContentSearchObjects/ConnectedContentSearchObjs';
import { ConnectedDetailObjectInfo } from './DetailObjectInfo';
import {
  ISearchObjectsProps,
  ISearchObjectsState
} from './SearchObjects.types';

const listPageStyles = require('@microsoft/portal-app/lib/styling/patterns/ListPage.scoped.scss');
const pageLayoutStyles = require('../../Styles/PageLayout.scoped.scss');

export class SearchObjects extends React.Component<
  ISearchObjectsProps,
  ISearchObjectsState
> {
  private pageFocusRef = React.createRef<HTMLDivElement>();
  constructor(props: ISearchObjectsProps) {
    super(props);
    this.state = {
      curObj: { objectId: '' },
      showPanel: false,
      showRAPanel: false
    };
  }

  public componentDidMount() {
    this._focus();
  }
  public render(): React.ReactNode {
    const { t } = this.props;
    return (
      <main
        data-automation-id="searchObjects"
        className={css('ms-ListPage', listPageStyles.listPage)}
      >
        <div
          className={css(
            'ms-ListPageHeader',
            listPageStyles.pageHeader,
            pageLayoutStyles.pageHeader
          )}
        >
          {/* listPageStyles.pageSectionHeader*/}
          <div
            role="heading"
            aria-level={1}
            tabIndex={-1}
            ref={this.pageFocusRef}
            className={css(
              'ms-PageTitle',
              listPageStyles.pageTitle,
              listPageStyles.pageSectionTitle
            )}
          >
            {t('Search')}
          </div>
          {/* <div role="note" className={listPageStyles.listPageDescription}>
            <span>
              {t('These are the apps that have permissions to access your data.')}
            </span>
          </div> */}
        </div>
        <div>
          <ConnectedContentSearchObjs selectObject={this._selectObject} />
          <ConnectedDetailObjectInfo
            showPanel={this.state.showPanel}
            dismissPanel={this._dismissPanel}
            curObj={this.state.curObj}
            showRAPanel={this._showRAPanel}
          />
          <ConnectedRoleAssignmentPanel
            showPanel={this.state.showRAPanel}
            roleAssignmentPanelType={this.state.raFormPanelType!}
            onDismiss={this._hideRAPanel}
          />
        </div>
      </main>
    );
  }

  private _focus = () => {
    const node = this.pageFocusRef.current;
    if (node) {
      node.focus();
    }
  }

  private _showRAPanel = (
    formPanelType: FormPanelType,
    showPanel: boolean
  ): void => {
    this.setState({ showRAPanel: showPanel, raFormPanelType: formPanelType });
  }
  private _hideRAPanel = (): void => {
    this.setState({
      showRAPanel: false
    });
  }
  private _selectObject = (obj?: IGraphObjectInfo) => {
    if (isUndefined(obj)) {
      this.setState({
        showPanel: false,
        curObj: {} as IGraphObjectInfo
      });
      return;
    }
    if (!this.state.showPanel || this.state.curObj === obj) {
      this.setState({
        showPanel: !this.state.showPanel
      });
    }
    this.setState({
      curObj: obj
    });
  }

  private _dismissPanel = () => {
    this.setState({
      showPanel: false,
      curObj: {} as IGraphObjectInfo
    });
  }
}



