import { IStyleFunctionOrObject } from '@uifabric/utilities';
import { TranslationFunction } from 'i18next';
import { IColumn, IDetailsListStyleProps, IDetailsListStyles } from 'office-ui-fabric-react/lib/DetailsList';
import { ResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import { GraphObjectTypes } from './GraphObjectTypes';

const detailListStyles = require('../../Styles/DetailList.scoped.scss');

export const STYLES_AAD_SYS_ROLE_ASSIGNS: IStyleFunctionOrObject<IDetailsListStyleProps, IDetailsListStyles> = {
  root: {
    maxHeight: 'calc(100vh - 200px)'
  }
};

export const getColAADSysRoleAssigns = (
  _responsiveMode: ResponsiveMode,
  t: TranslationFunction,
  typeGraphObject: GraphObjectTypes
): IColumn[] => {
  switch (typeGraphObject) {
    case GraphObjectTypes.User: {
      return [
        {
          key: 'displayName',
          name: t('Display Name'),
          fieldName: 'displayName',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 80,
          maxWidth: 250,
          isResizable: true
        },
        {
          key: 'email',
          name: t('Email'),
          fieldName: 'mail',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 60,
          maxWidth: 250,
          isResizable: true
        },
        {
          key: 'jobTitle',
          name: t('Title'),
          fieldName: 'jobTitle',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 60,
          maxWidth: 180,
          isResizable: true
        },
        {
          key: 'department',
          name: t('Department'),
          fieldName: 'department',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 60,
          maxWidth: 200,
          isResizable: true
        },
        {
          key: 'upn',
          name: t('UPN'),
          fieldName: 'userPrincipalName',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 60,
          maxWidth: 250,
          isResizable: true
        }
      ];
    }
    case GraphObjectTypes.Group: {
      return [
        {
          key: 'displayName',
          name: t('Display Name'),
          fieldName: 'displayName',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 80,
          maxWidth: 450,
          isResizable: true
        },
        {
          key: 'email',
          name: t('Email'),
          fieldName: 'mail',
          headerClassName: detailListStyles.tblCenterHeader,
          minWidth: 60,
          maxWidth: 160,
          isResizable: true
        },
        {
          key: 'description',
          name: t('Description'),
          fieldName: 'description',
          headerClassName: detailListStyles.tblCenterHeader,
          minWidth: 60,
          maxWidth: 160,
          isResizable: true
        }
      ];
    }
    case GraphObjectTypes.ServicePrincipal: {
      return [
        {
          key: 'displayName',
          name: t('Display Name'),
          fieldName: 'displayName',
          headerClassName: detailListStyles.tblHeader,
          minWidth: 80,
          maxWidth: 450,
          isResizable: true
        },
        {
          key: 'appId',
          name: t('Application ID'),
          fieldName: 'appId',
          headerClassName: detailListStyles.tblCenterHeader,
          minWidth: 60,
          maxWidth: 230,
          isResizable: true
        },
        {
          key: 'objectId',
          name: t('Principal ID'),
          fieldName: 'objectId',
          headerClassName: detailListStyles.tblCenterHeader,
          minWidth: 60,
          maxWidth: 230,
          isResizable: true
        },
        {
          key: 'homepage',
          name: t('Home Page'),
          fieldName: 'homepage',
          headerClassName: detailListStyles.tblCenterHeader,
          minWidth: 60,
          maxWidth: 300,
          isResizable: true
        }
      ];
    }
    default: {
      return [];
    }
  }
};



