import {
  CommandButton,
  css,
  Panel,
  PanelType,
  Persona,
  PersonaSize,
  Pivot,
  PivotItem
} from 'office-ui-fabric-react';
import * as React from 'react';
import { Route } from 'react-router-dom';
import { isNullOrUndefined, isUndefined } from 'util';
import { EmptyFilterSearchRA } from '../../../models/aadPAP/roleAssignment';
import { ConnectedRoleAssignmentsTable } from '../../RoleAssignment/RoleAssignmentsTable/ConnectedRoleAssignmentsTable';
import { FormPanelType } from '../../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import { DetailObjectInfoView } from '../DetailObjectInfoView/DetailObjectInfoView';
import {
  DetailObjectPivots,
  IDetailObjectInfoProps,
  IDetailObjectInfoState,
  panelStyles
} from './DetailObjectInfo.types';

const cardStyles = require('@microsoft/portal-app/lib/styling/patterns/Card.scoped.scss');
const detailsPageStyles = require('@microsoft/portal-app/lib/styling/patterns/DetailsPage.scoped.scss');

export class DetailObjectInfo extends React.Component<
  IDetailObjectInfoProps,
  IDetailObjectInfoState
> {
  constructor(props: IDetailObjectInfoProps) {
    super(props);
    this.state = { selectedPivot: DetailObjectPivots.UserDetails };
  }

  public render(): React.ReactNode {
    const { t } = this.props;
    const pivots = [];
    pivots.push(
      <PivotItem
        key="details"
        headerText={this.props.t('User Details')}
        itemKey={DetailObjectPivots.UserDetails}
      />
    );
    pivots.push(
      <PivotItem
        key="assignments"
        headerText={this.props.t('Role Assignments')}
        itemKey={DetailObjectPivots.RoleAssignments}
      />
    );
    return (
      <Panel
        isOpen={this.props.showPanel}
        onDismiss={this.onDismiss}
        isLightDismiss={true}
        type={PanelType.medium}
        headerText={t('Object Information')}
        isBlocking={true}
        styles={panelStyles}
      >
        <CommandButton
          ariaLabel={t(this.props.curObj.displayName!)}
          className={css(
            cardStyles.actionCard,
            cardStyles.card,
            cardStyles.cardIconHeaderAndDescription,
            cardStyles.cardPrimaryContent,
            cardStyles.cardListItem
          )}
        >
          <div className={css(cardStyles.cardContentIconAndText)}>
            <Persona
              hidePersonaDetails
              text={this.props.curObj.displayName}
              size={PersonaSize.regular}
            />
            <div title={'Name'} className={css(cardStyles.cardTitle)}>
              <span className="ms-pii"> {this.props.curObj.displayName}</span>
            </div>
          </div>
          <div
            title={t('Object Id')}
            className={css('ms-TileTwoLineBlock', cardStyles.cardDescription)}
          >
            <span className="ms-pii"> {this.props.curObj.objectId}</span>
          </div>
        </CommandButton>

        <div className={css('ms-DetailsPivot')}>
          <Pivot
            selectedKey={this.state.selectedPivot}
            onLinkClick={this._handlePivotClick}
            headersOnly={true}
          >
            {pivots}
          </Pivot>
        </div>
        <div className={css(detailsPageStyles.pivotContent)}>
          {this._renderGroupPivot()}
        </div>
      </Panel>
    );
  }

  private _renderGroupPivot(): JSX.Element {
    switch (this.state.selectedPivot) {
      case DetailObjectPivots.UserDetails:
        return (
          <Route
            component={() => (
              <DetailObjectInfoView graphObject={this.props.curObj} />
            )}
          />
        );
      case DetailObjectPivots.RoleAssignments:
        return (
          <Route
            component={() => (
              <ConnectedRoleAssignmentsTable
                setShowPanel={this._toggleShowPanel}
                textFilter={''}
                isSmallTable={true}
              />
            )}
          />
        );
      // return <Route component={() => <div>hello</div>} />;
      default:
        return <Route component={DetailObjectInfoView} />;
    }
  }

  private _toggleShowPanel = (showPanel: boolean, panelType: FormPanelType) => {
    this.onDismiss();
    this.props.showRAPanel(panelType, showPanel);
  }

  private _handlePivotClick = (item: PivotItem | undefined): void => {
    let newPivot = DetailObjectPivots.UserDetails;
    if (!item || !item.props) {
      return;
    }
    switch (item.props.itemKey) {
      case DetailObjectPivots.RoleAssignments:
        this.changeAssignedUsers();
        newPivot = DetailObjectPivots.RoleAssignments;
        break;
      case DetailObjectPivots.UserDetails:
      default:
        newPivot = DetailObjectPivots.UserDetails;
        break;
    }
    this.setState({
      selectedPivot: newPivot
    });
  }
  private changeAssignedUsers = (): void => {
    if (isNullOrUndefined(this.props.currentNpd.appId)) {
      return;
    }
    let roleAssignmentQueryParams = { ...this.props.roleAssignmentQueryParams };
    let filter = { ...EmptyFilterSearchRA };
    if (isUndefined(roleAssignmentQueryParams.filterSearchRA)) {
      roleAssignmentQueryParams.filterSearchRA = filter;
    }
    roleAssignmentQueryParams.appId = this.props.currentNpd.appId;
    roleAssignmentQueryParams.filterSearchRA.principalId = this.props.curObj.objectId;
    roleAssignmentQueryParams.filterSearchRA.roleDefinitionId = null;
    roleAssignmentQueryParams.skipToken = undefined;
    this.props.getListRAs(roleAssignmentQueryParams);
  }

  private onDismiss = (): void => {
    // let roleAssignmentQueryParams = { ...this.props.roleAssignmentQueryParams };
    // let filter = { ...EmptyFilterSearchRA };
    // roleAssignmentQueryParams.filterSearchRA = filter;
    // this.props.updateRAQuery(roleAssignmentQueryParams);
    // this.props.getListRAs(this.props.roleAssignmentQueryParams);
    this.props.dismissPanel();
    this.setState({
      selectedPivot: DetailObjectPivots.UserDetails
    });
  }
}



