import {
  DetailsRow,
  DetailsRowCheck,
  IDetailsRowCheckProps,
  IDetailsRowProps,
  IDetailsRowStyles,
  ISelection
} from 'office-ui-fabric-react/lib/DetailsList';
import { getTheme } from 'office-ui-fabric-react/lib/Styling';
import * as React from 'react';
import { isNullOrUndefined } from 'util';
import { FormPanelType } from '../../components/RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';

export const renderEveryOtherRow = (
  props: IDetailsRowProps | undefined
): JSX.Element => {
  const customStyles: Partial<IDetailsRowStyles> = {};
  const theme = getTheme();
  if (isNullOrUndefined(props)) {
    props = {} as IDetailsRowProps;
  }
  if (props.itemIndex % 2 === 0) {
    // Every other row renders with a different background color
    customStyles.root = { backgroundColor: theme.palette.themeLighterAlt };
  }

  return (
    <div tabIndex={0}>
      <DetailsRow {...props} styles={customStyles} />
    </div>
  );
};

export const renderEveryOtherRowRoleAssignment = (
  props: IDetailsRowProps | undefined,
  // tslint:disable-next-line:no-any
  setPanel: any,
  selection: ISelection
): JSX.Element => {
  const customStyles: Partial<IDetailsRowStyles> = {};
  const theme = getTheme();
  if (isNullOrUndefined(props)) {
    props = {} as IDetailsRowProps;
  }
  if (props.itemIndex % 2 === 0) {
    // Every other row renders with a different background color
    customStyles.root = { backgroundColor: theme.palette.themeLighterAlt };
  }

  return (
    <div
      onClick={() => {
        setPanel(true, FormPanelType.view);
      }}
      onKeyDown={(event: React.KeyboardEvent<HTMLDivElement>) => {
        if (event.key === 'Enter') {
          setPanel(true, FormPanelType.view);
        }
      }}
      tabIndex={0}
    >
      <DetailsRow
        {...props}
        styles={customStyles}
        onRenderCheck={(cProps: IDetailsRowCheckProps) =>
          renderCheck(cProps, selection, props!.itemIndex)
        }
      />
    </div>
  );
};

export const renderCheck = (
  props: IDetailsRowCheckProps,
  selection: ISelection,
  index: number
): JSX.Element => {
  return (
    <DetailsRowCheck
      onClick={(event: React.MouseEvent<HTMLDivElement>) => {
        event.stopPropagation();
        selection.toggleIndexSelected(index);
      }}
      {...props}
    />
  );
};

export const renderEveryOtherRowSearchObject = (
  props: IDetailsRowProps | undefined,
  // tslint:disable-next-line:no-any
  selectObject: any,
  selection: ISelection
): JSX.Element => {
  const customStyles: Partial<IDetailsRowStyles> = {};
  const theme = getTheme();
  if (isNullOrUndefined(props)) {
    props = {} as IDetailsRowProps;
  }
  if (props.itemIndex % 2 === 0) {
    // Every other row renders with a different background color
    customStyles.root = { backgroundColor: theme.palette.themeLighterAlt };
  }

  const selectedObject = selection.getItems()[props.itemIndex];
  return (
    <div
      onClick={() => {
        selectObject(selectedObject);
      }}
      onKeyDown={(event: React.KeyboardEvent<HTMLDivElement>) => {
        if (event.key === 'Enter') {
          selectObject(selectedObject);
        }
      }}
      tabIndex={0}
    >
      <DetailsRow
        {...props}
        styles={customStyles}
        onRenderCheck={(cProps: IDetailsRowCheckProps) =>
          renderCheck(cProps, selection, props!.itemIndex)
        }
      />
    </div>
  );
};



