import { TranslationFunction } from 'i18next';
import { IColumn } from 'office-ui-fabric-react/lib/DetailsList';
import { ResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import { IDisplayItemRoleAssignment } from '../../../models/aadPAP/roleAssignment';

export interface ITableDisplayRoleAssignmentColumn extends IColumn {
  fieldName: keyof IDisplayItemRoleAssignment;
  onRender?(item: IDisplayItemRoleAssignment): React.ReactNode;
}

export const getTableDisplayRoleAssignmentColumns = (
  responsiveMode: ResponsiveMode,
  _t: TranslationFunction
): ITableDisplayRoleAssignmentColumn[] => {
  let columns: ITableDisplayRoleAssignmentColumn[] = [
    {
      key: 'id',
      name: 'Role Assignment ID',
      fieldName: 'id',
      headerClassName: 'tbl-header',
      minWidth: 60,
      maxWidth: 230,
      isResizable: true
    }
  ];
  if (responsiveMode === ResponsiveMode.small) {
    columns.push({
      key: 'nameRD',
      name: 'Role Def Name',
      fieldName: 'nameRD',
      headerClassName: 'tbl-header',
      minWidth: 80,
      maxWidth: 240,
      isResizable: true
    });
    columns.push({
      key: 'nameObj',
      name: 'Display Name',
      fieldName: 'nameObj',
      headerClassName: 'tbl-header',
      minWidth: 80,
      maxWidth: 240,
      isResizable: true
    });
  }

  if (responsiveMode > ResponsiveMode.small) {
    columns.push({
      key: 'nameRD',
      name: 'Role Def Name',
      fieldName: 'nameRD',
      headerClassName: 'tbl-header',
      minWidth: 80,
      maxWidth: 240,
      isResizable: true
    });
    columns.push({
      key: 'principalId',
      name: 'PrincipalId',
      fieldName: 'principalId',
      headerClassName: 'tbl-header',
      minWidth: 60,
      maxWidth: 230,
      isResizable: true
    });
    columns.push({
      key: 'nameObj',
      name: 'Display Name',
      fieldName: 'nameObj',
      headerClassName: 'tbl-header',
      minWidth: 80,
      maxWidth: 240,
      isResizable: true
    });
    columns.push({
      key: 'scope',
      name: 'Scope',
      fieldName: 'scope',
      headerClassName: 'tbl-header',
      minWidth: 80,
      maxWidth: 240,
      isResizable: true
    });
    columns.push({
      key: 'condition',
      name: 'Condition',
      fieldName: 'condition',
      headerClassName: 'tbl-header',
      minWidth: 80,
      maxWidth: 240,
      isResizable: true
    });
  }
  return columns;
};



