import { css } from 'office-ui-fabric-react/lib/Utilities';
import * as React from 'react';
import { GraphObjectTypes, IGraphObjectInfo } from '../../../models/graph';

const detailPanelStyles = require('../../../Styles/PanelList.scoped.scss');

export const DetailObjectInfoView: React.StatelessComponent<{
  graphObject: IGraphObjectInfo;
}> = ({ graphObject }) => (
  <div>
    {getViewItem('Name', graphObject.displayName!)}
    {getViewItem('Object Id', graphObject.objectId!)}
    {graphObject.objectType === GraphObjectTypes.User ||
    graphObject.objectType === GraphObjectTypes.Group
      ? getViewItem('Email', graphObject.mail!)
      : null}
    {getViewItemByObjectType(graphObject)}
  </div>
);

export const getViewItemByObjectType = (graphObject: IGraphObjectInfo) => {
  switch (graphObject.objectType) {
    case GraphObjectTypes.Group:
      return <div>{getViewItem('Description', graphObject.description!)}</div>;
    case GraphObjectTypes.ServicePrincipal:
      return (
        <div>
          {getViewItem('Application ID', graphObject.appId!)}
          {getViewItem('Home Page', graphObject.homepage!)}
        </div>
      );
    case GraphObjectTypes.User:
    default:
      return (
        <div>
          {getViewItem('User Principal Name', graphObject.userPrincipalName!)}
          {getViewItem('Company', graphObject.companyName!)}
          {getViewItem('Department', graphObject.department!)}
          {getViewItem('Job Title', graphObject.jobTitle!)}
          {getViewItem('Telephone Number', graphObject.telephoneNumber!)}
        </div>
      );
  }
};

export const getViewItem = (fieldName: string, fieldValue: string) => {
  return (
    <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
      <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
        {fieldName}
      </div>
      <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
        {fieldValue}
      </div>
    </div>
  );
};



