import { FormikProps } from 'formik';
import { IWithResponsiveModeState } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import { InjectedTranslateProps } from 'react-i18next';
import * as Yup from 'yup';
import { ISelectedRoleAssignment } from '../../../models/aadPAP/roleAssignment/ISelectedRoleAssignment';
import {
  EMPTY_ROLE_DEFINITION_WITH_LOADING,
  IListRDsState,
  IRoleDefinition
} from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { IBulkGraphInfosState } from '../../../models/graph/IBulkGraphInfos';
import {
  EMPTY_GRAPH_OBJECT_INFO,
  IGraphObjectInfo
} from '../../../models/graph/IGraphObjectInfo';
import { GUID_PATTERN } from '../../../utilities/guid';
import { FormPanelType } from '../../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import { IListGraphObjsState } from '../RoleAssignmentsTable/RoleAssignmentsTable.types';

export interface IRoleAssignmentFormProps
  extends IRoleAssignmentFormMappedProps,
    IRoleAssignmentFormActions,
    IConnectedRoleAssignmentFormProps,
    InjectedTranslateProps,
    IWithResponsiveModeState {}

export interface IRoleAssignmentFormMappedProps {
  listGraphObjects: IListGraphObjsState;
  bulkGraphInfos: IBulkGraphInfosState;
}

/**
 * Used as a standalone interface by mapDispatchToProps, the IListPageActions interface defines the actions
 * for the ListPage control
 */
export interface IRoleAssignmentFormActions {
  getGraphObjectList: (graphObjectIds: IGraphObjectInfo[]) => void;
  getBulkGraphInfos(payload: { bulkObjectKeyWords: string }): void;
}

export interface IRoleAssignmentFormState {
  // editDisabled: boolean;
  // roleDefintionId: string;
  showModal: boolean;
  selectedGraphObject?: IGraphObjectInfo;
  // formPrincipleId: string;
}

export interface IConnectedRoleAssignmentFormProps {
  listRoleDefinitions: IListRDsState;
  roleAssignment: ISelectedRoleAssignment;
  roleAssignmentPanelType: FormPanelType;
  formikProps: FormikProps<RoleAssignmentFormValue>;
  isEditDisabled: boolean;
  // onUpdate(roleAssignment: ISelectedRoleAssignment): void;
  // onDelete(roleAssignment: ISelectedRoleAssignment): void;
}

export class RoleAssignmentFormValue {
  public id: string;
  public roleDefinitionId: string;
  public roleDefinitionName: string;
  public graphObjectInfo: IGraphObjectInfo;
  public roleDefinition: IRoleDefinition;
  public principalId: string;
  public nameRD: string;
  public nameObj: string;
  public scope: string;
  public condition: string;
  public isBuiltIn: boolean = false;
  public createdBy: string = '';
  public createdByPrincipal?: IGraphObjectInfo;
  public createdOn: string = ''; // change to Date later
  public updatedBy: string = '';
  public updatedByPrincipal?: IGraphObjectInfo;
  public updatedOn: string = '';
  public isLoading: boolean = false;
  public static validationSchema() {
    return Yup.object().shape({
      roleDefinitionId: Yup.string()
        .required('Role Assignment Id is required!')
        .matches(
          new RegExp(GUID_PATTERN),
          'Role Assignment Id should be valid Guid'
        ),
      principalId: Yup.string()
        .required('Principal Id is required!')
        .matches(new RegExp(GUID_PATTERN), 'Principal Id should be valid Guid'),
      scope: Yup.string().required('Scope can not be empty!')
    });
  }

  constructor(value: ISelectedRoleAssignment) {
    this.id = value.id || '';
    this.roleDefinitionId = value.roleDefinitionId || '';
    this.roleDefinitionName = value.nameRD || '';
    this.roleDefinition =
      value.roleDefinition || EMPTY_ROLE_DEFINITION_WITH_LOADING;
    this.graphObjectInfo = value.graphObjectInfo || EMPTY_GRAPH_OBJECT_INFO;
    this.principalId = value.principalId || '';
    this.nameRD = value.nameRD || '';
    this.nameObj = value.nameObj || '';
    this.scope = value.scope || '';
    this.condition = value.condition || '';
    this.isBuiltIn = value.isBuiltIn || false;
    this.createdBy = value.createdBy || '';
    this.createdOn = value.createdOn || '';
    this.updatedBy = value.updatedBy || '';
    this.updatedOn = value.updatedOn || '';
  }
}



